export enum ItemTypes {
  TRIANGLE = "Triangle",
  CIRCLE = "Ellipse",
  HEXAGON = "Hexagon",
  RECTANGLE = "Rectangle",
  STAR = "Star",
  PENTAGON = "Pentagon",
  HEARTH = "Hearth",
  TEXT = "Text",
  IMAGE = "Image",
}

export interface Drawable {
  id: string;
  type: string;
  name: string;
  dimensions: {
    x: number;
    y: number;
    w: number;
    h: number;
    angle: number;
  };
  appearance: {
    color: string;
    backgroundImage: unknown;
  };
  text: {
    font: string;
    size: number;
    bold: boolean;
    italic: boolean;
    underline: boolean;
    alignment: "left" | "center" | "right";
    value: string;
  };
}

export interface ChangeDimensionsRequest {
  x: number;
  y: number;
  w: number;
  h: number;
  angle: number;
}

export const changeDimensions = (
  newDimensions: ChangeDimensionsRequest,
  ctx: Drawable,
  canvas: { w: number; h: number },
): { drawable: Drawable; updated: boolean } => {
  const { w: canvasW, h: canvasH } = canvas;

  const { x, y, w, h, angle } = newDimensions;

  const cos = (deg: number): number => Math.cos((deg * Math.PI) / 180),
    sin = (deg: number): number => Math.sin((deg * Math.PI) / 180),
    abs = (v) => Math.abs(v);

  const c1 = abs(cos(angle)),
    c2 = abs(sin(angle));

  const h1 = h * c1 + w * c2,
    w1 = h * c2 + w * c1,
    x1 = x + w / 2 - w1 / 2,
    y1 = y + h / 2 - h1 / 2;

  const right = x1 + w1,
    bottom = y1 + h1;

  if (x1 <= 0 || y1 <= 0 || right >= canvasW || bottom >= canvasH) {
    return { drawable: ctx, updated: false };
  }

  return {
    drawable: {
      ...ctx,
      dimensions: {
        ...newDimensions,
      },
    },
    updated: true,
  };
};

export interface CreateItemRequest {
  type: string;
  dimensions?: {
    w: number;
    h: number;
    angle: number;
  };
  appearance?: {
    color?: string;
    backgroundImage?: unknown;
  };
  text?: {
    font: string;
    size: number;
    bold: boolean;
    italic: boolean;
    underline: boolean;
    alignment: "left" | "center" | "right";
    value: string;
  };
}

export const makeItem = (
  e: CreateItemRequest,
  canvas: { w: number; h: number },
): Drawable => {
  return {
    id: Math.random().toString(36).substring(7),
    type: e.type,
    name: e.type,
    dimensions: {
      angle: e?.dimensions?.angle ?? 0,
      w: e?.dimensions?.w ?? 300,
      h: e?.dimensions?.h ?? 300,
      x: canvas.w / 2 - (e?.dimensions?.w ?? 300) / 2,
      y: canvas.h / 2 - (e?.dimensions?.h ?? 300) / 2,
    },
    appearance: {
      color: e?.appearance?.color ?? "#000",
      backgroundImage: e?.appearance?.backgroundImage ?? "",
    },
    text: {
      font: e?.text?.font ?? "Arial",
      size: e?.text?.size ?? 72,
      bold: e?.text?.bold ?? false,
      italic: e?.text?.italic ?? false,
      underline: e?.text?.underline ?? false,
      value: "Text",
      alignment: e?.text?.alignment ?? "center",
    },
  };
};

export interface UpdateItemRequest {
  id: string;
  name?: string;
  dimensions?: {
    x?: number;
    y?: number;
    w?: number;
    h?: number;
    angle?: number;
  };
  appearance?: {
    color?: string;
    backgroundImage?: unknown;
  };
  text?: {
    font?: string;
    size?: number;
    bold?: boolean;
    italic?: boolean;
    underline?: boolean;
    alignment?: "left" | "center" | "right";
    value?: string;
  };
}

export const updateItem = (
  e: UpdateItemRequest,
  drawable: Drawable,
): Drawable => {
  const hasOwnProperty = (obj: unknown, path: string): boolean => {
    if (!path) return false;

    const properties = path.split(".");
    let object: unknown = obj;

    for (let i = 0; i < properties.length; i++) {
      const prop = properties[i];

      if (!object || !object.hasOwnProperty(prop)) {
        return false;
      } else {
        object = object[prop];
      }
    }

    return true;
  };

  drawable.name = hasOwnProperty(e, "name") ? e.name : drawable.name;

  drawable.dimensions.x = hasOwnProperty(e, "dimensions.x")
    ? e.dimensions.x
    : drawable.dimensions.x;

  drawable.dimensions.y = hasOwnProperty(e, "dimensions.y")
    ? e.dimensions.y
    : drawable.dimensions.y;

  drawable.dimensions.w = hasOwnProperty(e, "dimensions.w")
    ? e.dimensions.w
    : drawable.dimensions.w;

  drawable.dimensions.h = hasOwnProperty(e, "dimensions.h")
    ? e.dimensions.h
    : drawable.dimensions.h;

  drawable.dimensions.angle = hasOwnProperty(e, "dimensions.angle")
    ? e.dimensions.angle
    : drawable.dimensions.angle;

  drawable.appearance.color = hasOwnProperty(e, "appearance.color")
    ? e.appearance.color
    : drawable.appearance.color;

  drawable.appearance.backgroundImage = hasOwnProperty(
    e,
    "appearance.backgroundImage",
  )
    ? e.appearance.backgroundImage
    : drawable.appearance.backgroundImage;

  drawable.text.font = hasOwnProperty(e, "text.font")
    ? e.text.font
    : drawable.text.font;

  drawable.text.size = hasOwnProperty(e, "text.size")
    ? e.text.size
    : drawable.text.size;

  drawable.text.bold = hasOwnProperty(e, "text.bold")
    ? e.text.bold
    : drawable.text.bold;

  drawable.text.italic = hasOwnProperty(e, "text.italic")
    ? e.text.italic
    : drawable.text.italic;

  drawable.text.underline = hasOwnProperty(e, "text.underline")
    ? e.text.underline
    : drawable.text.underline;

  drawable.text.alignment = hasOwnProperty(e, "text.alignment")
    ? e.text.alignment
    : drawable.text.alignment;

  drawable.text.value = hasOwnProperty(e, "text.value")
    ? e.text.value
    : drawable.text.value;

  return drawable;
};
