import { Drawable } from "./Drawable";

export class Slide {
  id: string;
  items: Drawable[];
}

export class Canvas extends Slide {
  width: number;
  height: number;
  standardWidth: number;
  standardHeight: number;
  items: Drawable[];
  selectedItemId: string | null;
}

export const createSlide = (): Slide => ({
  id: Math.random().toString(),
  items: [],
});

export interface SetSelectedItemRequest {
  itemId: string;
}

export const setSelectedItem = (
  request: SetSelectedItemRequest,
  ctx: Canvas,
): void => {
  ctx.selectedItemId = request.itemId;
};

export interface ResizeCanvasRequest {
  width: number;
  height: number;
}

export const resizeCanvas = (
  request: ResizeCanvasRequest,
  ctx: Canvas,
): void => {
  ctx.width = request.width;
  ctx.height = request.height;
};

export const refreshSlide = (slides: Slide[], ctx: Canvas): void => {
  const slide = slides.find((s) => s.id === ctx.id);

  if (slide) {
    slide.items = ctx.items;
  }
};
