import { BaseCommand, registerCommand } from "./History";
import { Canvas, Slide } from "./Canvas";

const CHANGE_SLIDE_COMMAND = "CHANGE_SLIDE_COMMAND";

class ChangeSlideCommand extends BaseCommand {
  type: typeof CHANGE_SLIDE_COMMAND;
  prevId: string;
  nextId: string;
}

export interface ChangeSlideRequest {
  id: string;
}

export const makeChangeSlideCommand = (
  e: ChangeSlideRequest,
  ctx: Canvas,
): ChangeSlideCommand => {
  return {
    type: CHANGE_SLIDE_COMMAND,
    isFantom: true,
    nextId: e.id,
    prevId: ctx.id,
  };
};

registerCommand(CHANGE_SLIDE_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as ChangeSlideCommand;
    ctx.id = command.prevId;
    ctx.selectedItemId = null;
    ctx.items = items.find((i) => i.id === command.prevId).items;
  },
  execute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as ChangeSlideCommand;
    ctx.id = command.nextId;
    ctx.selectedItemId = null;
    ctx.items = items.find((i) => i.id === command.nextId).items;
  },
});
