import * as React from "react";
import { connect } from "react-redux";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { UIFactory as EditorUIFactory } from "../../infrastructure/UIFactory";
import { ItemTypes } from "../model/Drawable";

interface ControlPanelProps {
  currentItemId: string;
}

class ControlPanel extends React.Component<
  ControlPanelProps & DispatchProps,
  unknown
> {
  constructor(props: ControlPanelProps & DispatchProps) {
    super(props);
  }

  mapItemType(
    type:
      | "triangle"
      | "circle"
      | "hexagon"
      | "pentagon"
      | "rectangle"
      | "heart"
      | "star",
  ): ItemTypes {
    switch (type) {
      case "triangle":
        return ItemTypes.TRIANGLE;
      case "circle":
        return ItemTypes.CIRCLE;
      case "hexagon":
        return ItemTypes.HEXAGON;
      case "pentagon":
        return ItemTypes.PENTAGON;
      case "rectangle":
        return ItemTypes.RECTANGLE;
      case "heart":
        return ItemTypes.HEARTH;
      case "star":
        return ItemTypes.STAR;
    }
  }

  render(): JSX.Element {
    const { onCreateItem } = this.props;

    return (
      <EditorUIFactory.ControlPanel
        onChangeLang={() => console.log("onChangeLang")}
        onSave={() => console.log("onSave")}
        onUndo={() => this.props.onUndo()}
        onRedo={() => this.props.onRedo()}
        onAddText={() => onCreateItem({ type: ItemTypes.TEXT })}
        onAddImage={(e) =>
          onCreateItem({
            type: ItemTypes.IMAGE,
            appearance: { backgroundImage: e },
          })
        }
        onAddShape={(e) => onCreateItem({ type: this.mapItemType(e) })}
      />
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    currentItemId: editor.canvas.selectedItemId,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(ControlPanel);
