import * as React from "react";
import { Drawable } from "../model/Drawable";
import { DragDropBounded } from "../../../../lib/dnd/DragDropBounded";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { connect } from "react-redux";

export interface DrawableProps extends Drawable {
  onChangeText: (e: string) => void;
  onClick: () => void;
  isActive: boolean;
}

export interface DrawableContainerProps {
  id: string;
  item: Drawable;
  render: (props: DrawableProps) => JSX.Element;
  selectedItemId: string | null;
  cw: number;
  ch: number;
  sw: number;
  sh: number;
}

export interface DrawableContainerState {
  itemCopy: Drawable;
  isSelected: boolean;
}

class DrawableContainer extends React.Component<
  DrawableContainerProps & DispatchProps,
  DrawableContainerState
> {
  constructor(props: DrawableContainerProps & DispatchProps) {
    super(props);

    this.state = {
      isSelected: false,
      itemCopy: JSON.parse(JSON.stringify(this.props.item)),
    };
  }

  componentDidUpdate(
    prevProps: Readonly<DrawableContainerProps & DispatchProps>,
  ) {
    if (prevProps.item !== this.props.item) {
      this.setState({
        itemCopy: JSON.parse(JSON.stringify(this.props.item)),
      });
    }
  }

  onItemMove({ dx, dy }: { dx: number; dy: number }): void {
    const {
      itemCopy: {
        dimensions: { x, y, w, h, angle },
      },
    } = this.state;

    const { cw, sw } = this.props;

    this.setState({
      itemCopy: {
        ...this.state.itemCopy,
        dimensions: {
          x: x + (dx * sw) / cw,
          y: y + (dy * sw) / cw,
          w: w,
          h: h,
          angle,
        },
      },
    });
  }

  onItemResize({
    x,
    y,
    w,
    h,
  }: {
    x: number;
    y: number;
    w: number;
    h: number;
  }): void {
    const {
      itemCopy: {
        dimensions: { angle },
      },
    } = this.state;

    const { cw, sw } = this.props;

    this.setState({
      itemCopy: {
        ...this.state.itemCopy,
        dimensions: {
          x: (x * sw) / cw,
          y: (y * sw) / cw,
          w: (w * sw) / cw,
          h: (h * sw) / cw,
          angle,
        },
      },
    });
  }

  onItemRotate({ angle }: { angle: number }): void {
    this.setState({
      itemCopy: {
        ...this.state.itemCopy,
        dimensions: { ...this.state.itemCopy.dimensions, angle },
      },
    });
  }

  listenClickAwayListener(): void {
    const { setSelectedItemId } = this.props;
    setSelectedItemId({ itemId: null });
  }

  dispatchUpdateItemEvent(): void {
    const { onUpdateItem } = this.props;

    const {
      itemCopy: {
        dimensions: { x, y, w, h, angle },
      },
    } = this.state;

    onUpdateItem({
      id: this.props.id,
      dimensions: {
        x,
        y,
        w,
        h,
        angle,
      },
    });
  }

  onKeyDown(e: KeyboardEvent): void {
    const { id, item, onDeleteItem, onCopyToBuffer } = this.props;

    const element = (e.target as unknown) as Record<string, string>;

    if (element?.type === "textarea") {
      return;
    }

    if (e.ctrlKey) {
      if (e.code === "KeyC") {
        onCopyToBuffer({ item });
      } else if (e.code === "KeyX") {
        onCopyToBuffer({ item });
        onDeleteItem({ id });
      }
    } else {
      if (e.code === "Backspace") {
        onDeleteItem({ id });
      }
    }
  }

  render(): JSX.Element {
    const { cw, sw, render } = this.props;

    const { x, y, w, h, angle } = this.state.itemCopy.dimensions;

    const newX = (x * cw) / sw,
      newY = (y * cw) / sw,
      newW = (w * cw) / sw,
      newH = (h * cw) / sw,
      size = (this.props.item.text.size * cw) / sw;
    const rendered = render({
      ...this.state.itemCopy,
      dimensions: {
        ...this.state.itemCopy.dimensions,
        x: newX,
        y: newY,
        w: newW,
        h: newH,
      },
      text: {
        ...this.props.item.text,
        size,
      },
      onClick: () =>
        this.props.setSelectedItemId({
          itemId: this.props.id,
        }),
      onChangeText: (e) =>
        this.props.onUpdateItem({ id: this.props.id, text: { value: e } }),
      isActive: this.props.id === this.props.selectedItemId,
    });

    return (
      <React.Fragment>
        <div tabIndex={0} onKeyDown={(e) => this.onKeyDown(e.nativeEvent)}>
          {rendered}
        </div>
        {this.props.selectedItemId === this.props.id && (
          <DragDropBounded
            x={newX}
            y={newY}
            w={newW}
            h={newH}
            angle={angle}
            onMove={(e) => this.onItemMove(e)}
            onMoveOver={() => this.dispatchUpdateItemEvent()}
            onResize={(e) => this.onItemResize(e)}
            onResizeOver={() => this.dispatchUpdateItemEvent()}
            onRotate={(e) => this.onItemRotate(e)}
            onRotateOver={() => this.dispatchUpdateItemEvent()}
            onClickAway={() => this.listenClickAwayListener()}
            onKeyDown={(e) => this.onKeyDown(e)}
          >
            <div />
          </DragDropBounded>
        )}
      </React.Fragment>
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    selectedItemId: editor.canvas.selectedItemId,
    cw: editor.canvas.width,
    ch: editor.canvas.height,
    sw: editor.canvas.standardWidth,
    sh: editor.canvas.standardHeight,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(DrawableContainer);
