import { BaseCommand, registerCommand } from "./History";
import { Canvas, Slide } from "./Canvas";

const DELETE_SLIDE_COMMAND = "DELETE_SLIDE_COMMAND";

class DeleteSlideCommand extends BaseCommand {
  type: typeof DELETE_SLIDE_COMMAND;
  before: Slide[];
  after: Slide[];
  deletedSlideId: string;
}

export interface DeleteSlideRequest {
  id: string;
}

export const makeDeleteSlideCommand = (
  e: DeleteSlideRequest,
  ctx: Canvas,
  slides: Slide[],
): DeleteSlideCommand => {
  const before = JSON.parse(JSON.stringify(slides));

  let after: Slide[] = JSON.parse(JSON.stringify(slides));

  after = after.filter((s) => s.id !== e.id);

  return {
    isFantom: false,
    type: DELETE_SLIDE_COMMAND,
    before,
    after,
    deletedSlideId: e.id,
  };
};

registerCommand(DELETE_SLIDE_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as DeleteSlideCommand;
    items.splice(0, items.length);
    command.before.forEach((i) => items.push(i));
  },
  execute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as DeleteSlideCommand;
    items.splice(0, items.length);
    command.after.forEach((i) => items.push(i));

    if (command.deletedSlideId === ctx.id) {
      const mapped: Map<string, string> = new Map<string, string>();

      command.before.forEach((v, i, arr) => {
        if (i !== arr.length - 1) {
          mapped.set(v.id, arr[i + 1].id);
          return;
        }

        console.log(v, i, arr);

        mapped.set(v.id, arr[i - 1].id);
      });

      ctx.id = mapped.get(command.deletedSlideId);
      ctx.selectedItemId = null;
      ctx.items = command.before.find((s) => s.id === ctx.id).items;
    }
  },
});
