import * as React from "react";
import { calcWrapperBounds } from "./utils/bound";
import DragDrop, { Props } from "./DragDrop";
import "./styles/index.scss";

interface DragDropBoundedProps extends Props {
  style?: React.CSSProperties;
  children: JSX.Element[] | JSX.Element;
  onClickAway?: () => void;
}

interface DragDropBoundedState {
  x1: number;
  y1: number;
  h1: number;
  w1: number;
  actionStarted: boolean;
}

export class DragDropBounded extends React.Component<
  DragDropBoundedProps,
  DragDropBoundedState
> {
  containerRef: HTMLDivElement | null;

  dndRef: React.RefObject<DragDrop>;

  constructor(props: DragDropBoundedProps) {
    super(props);

    this.dndRef = React.createRef();

    this.state = {
      h1: 0,
      w1: 0,
      y1: 0,
      x1: 0,
      actionStarted: false,
    };
  }

  componentDidMount(): void {
    this.onMove({ dx: 0, dy: 0 });
  }

  onMove(e: { dx: number; dy: number }): void {
    const { x, y, w, h, angle, onMove } = this.props;

    const { x1, y1, w1, h1 } = calcWrapperBounds({
      x: x + e.dx,
      y: y + e.dy,
      w,
      h,
      angle,
    });

    let dx = e.dx,
      dy = e.dy;

    if (y1 < 0) {
      dy = 0;
    }
    if (x1 < 0) {
      dx = 0;
    }
    if (this.containerRef.offsetWidth < x1 + w1) {
      dx = 0;
    }
    if (this.containerRef.offsetHeight < y1 + h1) {
      dy = 0;
    }

    this.updateBoundsOfRectangle({ x: x + dx, y: y + dy, w, h, angle });
    onMove({ dx, dy });
  }

  onResize(e: { x: number; y: number; w: number; h: number }): void {
    const { x, y, w, h, angle, onResize } = this.props;

    const { x1, y1, w1, h1 } = calcWrapperBounds({
      x: e.x,
      y: e.y,
      w: e.w,
      h: e.h,
      angle,
    });

    let newX = e.x,
      newY = e.y,
      newW = e.w,
      newH = e.h;

    if (
      x1 < 0 ||
      y1 < 0 ||
      this.containerRef.offsetWidth < x1 + w1 ||
      this.containerRef.offsetHeight < y1 + h1
    ) {
      newX = x;
      newY = y;
      newW = w;
      newH = h;
    }

    this.updateBoundsOfRectangle({ x: newX, y: newY, w: newW, h: newH, angle });

    onResize({ x: newX, y: newY, w: newW, h: newH });
  }

  onRotate(e: { angle: number }): void {
    const { x, y, w, h, angle: oldAngle, onRotate } = this.props;

    const { x1, y1, w1, h1 } = calcWrapperBounds({
      x,
      y,
      w,
      h,
      angle: e.angle,
    });

    let angle = e.angle;

    if (
      x1 < 0 ||
      y1 < 0 ||
      x1 + w1 > this.containerRef.offsetWidth ||
      y1 + h1 > this.containerRef.offsetHeight
    ) {
      angle = oldAngle;
    }

    this.updateBoundsOfRectangle({ ...this.props, angle });

    onRotate({ angle });
  }

  updateBoundsOfRectangle(e: {
    w: number;
    h: number;
    x: number;
    y: number;
    angle: number;
  }): void {
    const { x1, y1, w1, h1 } = calcWrapperBounds(e);

    this.setState({
      x1: x1,
      y1: y1,
      w1: w1,
      h1: h1,
    });
  }

  delayEventOver(): void {
    setTimeout(() => {
      this.setState({ actionStarted: false });
    }, 100);
  }

  notifyClickAway(e: MouseEvent): void {
    if (!this.state.actionStarted) {
      if (e.target === this.containerRef) {
        this.props.onClickAway ? this.props.onClickAway() : null;
      }

      if (!this.containerRef.contains(e.target as Element)) {
        this.props.onClickAway ? this.props.onClickAway() : null;
      }
    }
  }

  render(): JSX.Element {
    const { children, x, y, w, h, angle, style } = this.props;

    return (
      <React.Fragment>
        <div
          className={`area-selector-container`}
          ref={(e) => (this.containerRef = e)}
          style={style}
          onClick={(e) => this.notifyClickAway(e.nativeEvent)}
        >
          {children}
          <DragDrop
            {...this.props}
            x={x}
            y={y}
            w={w}
            h={h}
            angle={angle}
            onMoveStart={() => {
              this.setState({ actionStarted: true });
              if (this.props.onMoveStart) this.props.onMoveStart();
            }}
            onMove={(e) => this.onMove(e)}
            onMoveOver={() => {
              this.delayEventOver();
              if (this.props.onMoveOver) this.props.onMoveOver();
            }}
            onResizeStart={() => {
              this.setState({ actionStarted: true });
              if (this.props.onResizeStart) this.props.onResizeStart();
            }}
            onResize={(e) => this.onResize(e)}
            onResizeOver={() => {
              this.delayEventOver();
              if (this.props.onResizeOver) this.props.onResizeOver();
            }}
            onRotateStart={() => {
              this.setState({ actionStarted: true });
              if (this.props.onRotateStart) this.props.onRotateStart();
            }}
            onRotate={(e) => this.onRotate(e)}
            onRotateOver={() => {
              this.delayEventOver();
              if (this.props.onRotateOver) this.props.onRotateOver();
            }}
            ref={this.dndRef}
          />
        </div>
      </React.Fragment>
    );
  }
}
