import * as React from "react";
import {
  Select,
  Divider,
  MenuItem,
  TextField,
  FormControl,
  InputLabel,
  IconButton,
  Popper,
  Paper,
  ClickAwayListener,
  Fade,
  Button,
  Tooltip,
} from "@material-ui/core";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import SyncAltSharpIcon from "@material-ui/icons/SyncAltSharp";
import SwapVertSharpIcon from "@material-ui/icons/SwapVertSharp";
import SignalCellular4BarSharpIcon from "@material-ui/icons/SignalCellular4BarSharp";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import FormatBoldIcon from "@material-ui/icons/FormatBold";
import FormatItalicIcon from "@material-ui/icons/FormatItalic";
import FormatUnderlinedIcon from "@material-ui/icons/FormatUnderlined";
import DeleteIcon from "@material-ui/icons/Delete";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import FormatAlignLeftIcon from "@material-ui/icons/FormatAlignLeft";
import FormatAlignCenterIcon from "@material-ui/icons/FormatAlignCenter";
import FormatAlignRightIcon from "@material-ui/icons/FormatAlignRight";
import { SketchPicker } from "react-color";
import { ShapeControlProps } from "../../application/ui/EditorUIFactory";
import "./styles/shape-control-panel.scss";

interface DimensionsControlProps {
  x: number;
  y: number;
  w: number;
  h: number;
  angle: number;
  color: string;
  onChangeDimensions: (update: {
    x: number;
    y: number;
    w: number;
    h: number;
    angle: number;
  }) => void;
  onChangeColor: (update: { color: string }) => void;
  onChangeColorOver: (update: { color: string }) => void;
}

const DimensionsControl = ({
  x,
  y,
  w,
  h,
  angle,
  color,
  onChangeDimensions,
  onChangeColor,
  onChangeColorOver,
}: DimensionsControlProps): JSX.Element => {
  const [colorButtonRef, setColorButtonRef] = React.useState(null);

  const [colorPickerOpen, setColorPickerOpen] = React.useState(false);

  const confirmChanges = (e: {
    x?: number;
    y?: number;
    w?: number;
    h?: number;
    angle?: number;
    color?: string;
  }) => {
    if (e.hasOwnProperty("color")) {
      onChangeColor({
        color: e.color,
      });

      return;
    }

    onChangeDimensions({
      x: e.hasOwnProperty("x") && !isNaN(e.x) ? e.x : x,
      y: e.hasOwnProperty("y") && !isNaN(e.y) ? e.y : y,
      w: e.hasOwnProperty("w") && !isNaN(e.w) ? e.w : w,
      h: e.hasOwnProperty("h") && !isNaN(e.h) ? e.h : h,
      angle: e.hasOwnProperty("angle") && !isNaN(e.angle) ? e.angle : angle,
    });
  };

  return (
    <div className="mui-shape-control-container">
      <div className="mui-shape-control-item">
        <TextField
          type="number"
          InputProps={{
            startAdornment: <ArrowForwardIcon fontSize="small" />,
          }}
          size="small"
          fullWidth
          variant="outlined"
          label="x"
          value={x}
          onChange={(e) =>
            confirmChanges({ x: parseInt(e.currentTarget.value) })
          }
          disabled
        />
      </div>
      <div className="mui-shape-control-item">
        <TextField
          type="number"
          InputProps={{
            startAdornment: <ArrowUpwardIcon fontSize="small" />,
          }}
          size="small"
          fullWidth
          label="y"
          variant="outlined"
          value={y}
          onChange={(e) =>
            confirmChanges({ y: parseInt(e.currentTarget.value) })
          }
          disabled
        />
      </div>
      <div className="mui-shape-control-item">
        <TextField
          type="number"
          InputProps={{
            inputProps: { min: 0, max: 360 },
            startAdornment: (
              <SignalCellular4BarSharpIcon
                style={{ transform: "scale(-1, 1)" }}
                fontSize="small"
              />
            ),
          }}
          size="small"
          fullWidth
          label=" angle"
          variant="outlined"
          value={angle}
          onChange={(e) =>
            confirmChanges({ angle: parseInt(e.currentTarget.value) })
          }
          disabled
        />
      </div>
      <div className="mui-shape-control-item">
        <TextField
          type="number"
          InputProps={{
            startAdornment: <SyncAltSharpIcon fontSize="small" />,
          }}
          size="small"
          fullWidth
          variant="outlined"
          label="w"
          value={w}
          onChange={(e) =>
            confirmChanges({ w: parseInt(e.currentTarget.value) })
          }
          disabled
        />
      </div>
      <div className="mui-shape-control-item">
        <TextField
          type="number"
          InputProps={{
            startAdornment: <SwapVertSharpIcon fontSize="small" />,
          }}
          size="small"
          fullWidth
          variant="outlined"
          label="h"
          value={h}
          onChange={(e) =>
            confirmChanges({ h: parseInt(e.currentTarget.value) })
          }
          disabled
        />
      </div>
      <div className="mui-shape-control-color-button">
        <IconButton
          size="medium"
          onClick={(e) => {
            setColorButtonRef(e.currentTarget);
            setColorPickerOpen(true);
          }}
        >
          <ColorLensIcon fontSize="inherit" style={{ color }} />
        </IconButton>
      </div>
      <Popper
        style={{
          zIndex: 2147483647,
          visibility: "visible",
        }}
        open={colorPickerOpen}
        anchorEl={colorButtonRef}
        transition
        disablePortal={false}
        placement="left"
      >
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <div>
              <ClickAwayListener
                onClickAway={() => {
                  setColorPickerOpen(false);
                }}
              >
                <Paper elevation={0} className="mui-container">
                  <SketchPicker
                    color={color}
                    onChange={(e) => confirmChanges({ color: e.hex })}
                    onChangeComplete={(e) =>
                      onChangeColorOver({ color: e.hex })
                    }
                  />
                </Paper>
              </ClickAwayListener>
            </div>
          </Fade>
        )}
      </Popper>
    </div>
  );
};

interface FontControlProps {
  font: string;
  size: number;
  bold: boolean;
  italic: boolean;
  underline: boolean;
  onChange: (e: {
    font: string;
    size: number;
    bold: boolean;
    italic: boolean;
    underline: boolean;
    alignment: "left" | "center" | "right";
  }) => void;
  alignment: "left" | "center" | "right";
}

const FontControl = ({
  font,
  size,
  bold,
  italic,
  underline,
  alignment,
  onChange,
}: FontControlProps): JSX.Element => {
  const sizes = [6, 7, 8, 9, 10, 11, 12, 14, 18, 24, 30, 36, 48, 60, 72, 96];

  const fonts = [
    "Arial",
    "Calibri",
    "Cambria",
    "Courier New",
    "Helvetica",
    "Tahoma",
    "Times New Roman",
    "Verdana",
  ];

  const doUpdate = (e: {
    font?: string;
    size?: number;
    bold?: boolean;
    italic?: boolean;
    underline?: boolean;
    alignment?: "left" | "center" | "right";
  }) => {
    onChange({
      font: e.hasOwnProperty("font") ? e.font : font,
      size: e.hasOwnProperty("size") ? e.size : size,
      bold: e.hasOwnProperty("bold") ? e.bold : bold,
      italic: e.hasOwnProperty("italic") ? e.italic : italic,
      underline: e.hasOwnProperty("underline") ? e.underline : underline,
      alignment: e.hasOwnProperty("alignment") ? e.alignment : alignment,
    });
  };

  return (
    <div className="mui-shape-control-container">
      <div className="mui-shape-control-item mui-shape-control-font-size">
        <FormControl size="small" fullWidth>
          <InputLabel>Size</InputLabel>
          <Select
            style={{ textAlign: "center" }}
            size="small"
            label="Size"
            value={size}
            onChange={(e) =>
              doUpdate({ size: parseInt(e.target.value as string) })
            }
          >
            {sizes.map((i, idx) => (
              <MenuItem key={idx} value={i} style={{ textAlign: "center" }}>
                {i}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
      <div className="mui-shape-control-item mui-shape-control-font-family">
        <FormControl size="small" fullWidth>
          <InputLabel>Font</InputLabel>
          <Select
            size="small"
            label="Font"
            value={font}
            onChange={(e) => doUpdate({ font: e.target.value as string })}
          >
            {fonts.map((i, idx) => (
              <MenuItem key={idx} value={i} style={{ fontFamily: i }}>
                {i}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
      <div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={bold ? "outlined" : "text"}
            onClick={() => doUpdate({ bold: !bold })}
          >
            <FormatBoldIcon fontSize="medium" />
          </Button>
        </div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={italic ? "outlined" : "text"}
            onClick={() => doUpdate({ italic: !italic })}
          >
            <FormatItalicIcon fontSize="medium" />
          </Button>
        </div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={underline ? "outlined" : "text"}
            onClick={() => doUpdate({ underline: !underline })}
          >
            <FormatUnderlinedIcon fontSize="medium" />
          </Button>
        </div>
      </div>
      <div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={alignment === "left" ? "outlined" : "text"}
            onClick={() => doUpdate({ alignment: "left" })}
          >
            <FormatAlignLeftIcon fontSize="medium" />
          </Button>
        </div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={alignment === "center" ? "outlined" : "text"}
            onClick={() => doUpdate({ alignment: "center" })}
          >
            <FormatAlignCenterIcon fontSize="medium" />
          </Button>
        </div>
        <div className="mui-shape-control-icon-button-container">
          <Button
            className="mui-shape-control-icon-button"
            size="medium"
            variant={alignment === "right" ? "outlined" : "text"}
            onClick={() => doUpdate({ alignment: "right" })}
          >
            <FormatAlignRightIcon fontSize="medium" />
          </Button>
        </div>
      </div>
    </div>
  );
};

class MuiShapeControlPanel extends React.Component<ShapeControlProps, unknown> {
  constructor(props: ShapeControlProps) {
    super(props);
  }

  render(): JSX.Element {
    const {
      onChangeFont,
      onCopyItem,
      onDeleteItem,
      displayFontPanel,
    } = this.props;

    return (
      <div>
        <DimensionsControl {...this.props} />
        {displayFontPanel && (
          <React.Fragment>
            <Divider />
            <FontControl {...this.props} onChange={(e) => onChangeFont(e)} />
          </React.Fragment>
        )}
        <Divider />
        <div
          className="mui-shape-control-container"
          style={{ justifyContent: "right" }}
        >
          <div className="mui-shape-control-icon-button-container">
            <Tooltip title="Copy Item">
              <Button
                variant="outlined"
                className="mui-shape-control-icon-button"
                size="small"
                onClick={() => onCopyItem()}
              >
                <FileCopyIcon fontSize="small" />
              </Button>
            </Tooltip>
          </div>
          <div className="mui-shape-control-icon-button-container">
            <Button
              variant="outlined"
              className="mui-shape-control-icon-button"
              color="error"
              size="small"
              onClick={() => onDeleteItem()}
            >
              <DeleteIcon fontSize="small" />
            </Button>
          </div>
        </div>
      </div>
    );
  }
}

export default MuiShapeControlPanel;
