import { createAction, createReducer, createSlice } from "@reduxjs/toolkit";
import { createEditor, Editor } from "../../application/model/Editor";
import {
  undo as makeUndo,
  redo as makeRedo,
} from "../../application/model/History";
import {
  CreateItemRequest,
  makeItem,
  updateItem as doUpdateItem,
  UpdateItemRequest,
  changeDimensions as doChangeDimensions,
  ChangeDimensionsRequest,
} from "../../application/model/Drawable";
import { execute } from "../../application/model/History";
import { makeIncludeItemCommand } from "../../application/model/IncludeItemCommand";
import {
  refreshSlide,
  resizeCanvas,
  ResizeCanvasRequest,
  setSelectedItem as setItem,
  SetSelectedItemRequest,
} from "../../application/model/Canvas";
import { makeUpdateItemCommand } from "../../application/model/UpdateItemCommand";
import { makeDeleteItemCommand } from "../../application/model/DeleteItemCommand";
import {
  ChangeOrderPayload,
  createChangeOrderCommand,
} from "../../application/model/ChangeOrderCommand";
import { makeCreateSlideCommand } from "../../application/model/CreateSlideCommand";
import {
  DeleteSlideRequest,
  makeDeleteSlideCommand,
} from "../../application/model/DeleteSlideCommand";
import {
  ChangeSlideRequest,
  makeChangeSlideCommand,
} from "../../application/model/ChangeSlideCommand";
import {
  copyItemToBuffer,
  CopyItemToBufferPayload,
} from "../../application/model/Buffer";
import {
  ChangeSlideOrderRequest,
  makeChangeSlideOrderCommand,
} from "../../application/model/ChangeSlideOrder";

createSlice({
  name: "VISUAL_EDITOR",
  initialState: createEditor(),
  reducers: {},
});

enum ActionTypes {
  RESIZE_CANVAS = "editor/resizeCanvas",
  SET_SELECTED_ITEM = "editor/setSelectedItem",
  CREATE_ITEM = "editor/createItem",
  UPDATE_ITEM = "editor/updateItem",
  DELETE_ITEM = "editor/deleteItem",
  CHANGE_ITEM_DIMENSIONS = "editor/changeItemDimensions",
  CHANGE_ORDER = "editor/changeOrder",
  UNDO = "editor/undo",
  REDO = "editor/redo",
  CREATE_SLIDE = "editor/createSlide",
  DELETE_SLIDE = "editor/deleteSlide",
  CHANGE_SLIDE = "editor/changeSlide",
  COPY_TO_BUFFER = "editor/copyToBuffer",
  INSERT_FROM_BUFFER = "editor/insertFromBuffer",
  CHANGE_SLIDE_ORDER = "editor/changeSlideOrder",
}

export const changeSlideOrder = createAction<ChangeSlideOrderRequest>(
  ActionTypes.CHANGE_SLIDE_ORDER,
);

export const copyToBuffer = createAction<CopyItemToBufferPayload>(
  ActionTypes.COPY_TO_BUFFER,
);

export const insertFromBuffer = createAction<CopyItemToBufferPayload>(
  ActionTypes.INSERT_FROM_BUFFER,
);

export const createSlide = createAction(ActionTypes.CREATE_SLIDE);

export const deleteSlide = createAction<DeleteSlideRequest>(
  ActionTypes.DELETE_SLIDE,
);

export const changeSlide = createAction<ChangeSlideRequest>(
  ActionTypes.CHANGE_SLIDE,
);

export const setSelectedItem = createAction<SetSelectedItemRequest>(
  ActionTypes.SET_SELECTED_ITEM,
);

export const createItem = createAction<CreateItemRequest>(
  ActionTypes.CREATE_ITEM,
);

export const updateItem = createAction<UpdateItemRequest>(
  ActionTypes.UPDATE_ITEM,
);

export const deleteItem = createAction<{ id: string }>(ActionTypes.DELETE_ITEM);

export const undo = createAction<CreateItemRequest>(ActionTypes.UNDO);

export const redo = createAction<CreateItemRequest>(ActionTypes.REDO);

export const changeOrder = createAction<ChangeOrderPayload>(
  ActionTypes.CHANGE_ORDER,
);

export const resizeWindow = createAction<ResizeCanvasRequest>(
  ActionTypes.RESIZE_CANVAS,
);

interface ChangeDimensionsReq extends ChangeDimensionsRequest {
  id: string;
}

export const changeItemDimensions = createAction<ChangeDimensionsReq>(
  ActionTypes.CHANGE_ITEM_DIMENSIONS,
);

export const reducer = createReducer<Editor>(createEditor(), (builder) => {
  builder
    .addCase(createItem, (state, payload) => {
      const item = makeItem(payload.payload, {
        w: state.canvas.standardWidth,
        h: state.canvas.standardHeight,
      });
      execute(
        makeIncludeItemCommand(item),
        state.canvas,
        state.slides,
        state.history,
      );
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(updateItem, (state, payload) => {
      const item = state.canvas.items.find((i) => i.id === payload.payload.id);
      const before = JSON.parse(JSON.stringify(item));
      const after = doUpdateItem(payload.payload, item);
      execute(
        makeUpdateItemCommand({ before, after }),
        state.canvas,
        state.slides,
        state.history,
      );
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(deleteItem, (state, payload) => {
      execute(
        makeDeleteItemCommand({
          before: JSON.parse(JSON.stringify(state.canvas.items)),
          after: state.canvas.items.filter((i) => i.id !== payload.payload.id),
        }),
        state.canvas,
        state.slides,
        state.history,
      );
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(changeItemDimensions, (state, payload) => {
      const item = state.canvas.items.find((i) => i.id === payload.payload.id);
      const before = JSON.parse(JSON.stringify(item));

      const { drawable, updated } = doChangeDimensions(payload.payload, item, {
        w: state.canvas.standardWidth,
        h: state.canvas.standardHeight,
      });

      if (updated) {
        execute(
          makeUpdateItemCommand({ before, after: drawable }),
          state.canvas,
          state.slides,

          state.history,
        );
      }
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(undo, (state) => {
      makeUndo(state.canvas, state.slides, state.history);
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(redo, (state) => {
      makeRedo(state.canvas, state.slides, state.history);
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(setSelectedItem, (state, payload) => {
      setItem(payload.payload, state.canvas);
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(resizeWindow, (state, payload) => {
      resizeCanvas(payload.payload, state.canvas);
    })
    .addCase(changeOrder, (state, payload) => {
      execute(
        createChangeOrderCommand(payload.payload, state.canvas),
        state.canvas,
        state.slides,
        state.history,
      );
      refreshSlide(state.slides, state.canvas);
    })
    .addCase(createSlide, (s) =>
      execute(
        makeCreateSlideCommand(s.canvas, s.slides),
        s.canvas,
        s.slides,
        s.history,
      ),
    )
    .addCase(deleteSlide, (s, p) => {
      execute(
        makeDeleteSlideCommand(p.payload, s.canvas, s.slides),
        s.canvas,
        s.slides,
        s.history,
      );
    })
    .addCase(changeSlide, (s, p) => {
      execute(
        makeChangeSlideCommand(p.payload, s.canvas),
        s.canvas,
        s.slides,
        s.history,
      );
    })
    .addCase(copyToBuffer, (s, p) => {
      copyItemToBuffer(p.payload, s.buffer);
    })
    .addCase(insertFromBuffer, (s) => {
      if (s.buffer.value) {
        const item = makeItem(s.buffer.value, {
          w: s.canvas.standardWidth,
          h: s.canvas.standardHeight,
        });
        execute(makeIncludeItemCommand(item), s.canvas, s.slides, s.history);
        refreshSlide(s.slides, s.canvas);
      }
    })
    .addCase(changeSlideOrder, (s, r) => {
      const cmd = makeChangeSlideOrderCommand(r.payload, s.slides);
      execute(cmd, s.canvas, s.slides, s.history);
    });
  return;
});

export interface DispatchProps {
  setSelectedItemId: (values: SetSelectedItemRequest) => void;
  onCreateItem: (values: CreateItemRequest) => void;
  onDeleteItem: (values: { id: string }) => void;
  onResizeWindow: (values: ResizeCanvasRequest) => void;
  onUpdateItem: (values: UpdateItemRequest) => void;
  onChangeDimensionsManually: (values: ChangeDimensionsReq) => void;
  onChangeOrder: (values: ChangeOrderPayload) => void;
  onCreateSlide: () => void;
  onDeleteSlide: (values: DeleteSlideRequest) => void;
  onChangeSlide: (values: ChangeSlideRequest) => void;
  onCopyToBuffer: (values: CopyItemToBufferPayload) => void;
  onInsertFromBuffer: () => void;
  onChangeSlideOrder: (values: ChangeSlideOrderRequest) => void;
  onUndo: () => void;
  onRedo: () => void;
}

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export const mapDispatchToProps = (dispatch): DispatchProps => ({
  setSelectedItemId: (...args) => dispatch(setSelectedItem(...args)),
  onResizeWindow: (...args) => dispatch(resizeWindow(...args)),
  onCreateItem: (...args) => dispatch(createItem(...args)),
  onUpdateItem: (...args) => dispatch(updateItem(...args)),
  onDeleteItem: (...args) => dispatch(deleteItem(...args)),
  onChangeDimensionsManually: (...args) =>
    dispatch(changeItemDimensions(...args)),
  onUndo: (...args) => dispatch(undo(...args)),
  onRedo: (...args) => dispatch(redo(...args)),
  onChangeOrder: (...args) => dispatch(changeOrder(...args)),
  onCreateSlide: (...args) => dispatch(createSlide(...args)),
  onDeleteSlide: (...args) => dispatch(deleteSlide(...args)),
  onChangeSlide: (...args) => dispatch(changeSlide(...args)),
  onCopyToBuffer: (...args) => dispatch(copyToBuffer(...args)),
  onInsertFromBuffer: (...args) => dispatch(insertFromBuffer(...args)),
  onChangeSlideOrder: (...args) => dispatch(changeSlideOrder(...args)),
});

interface RootState {
  editor: Editor;
}

export const createMapStateToProps = (
  state: RootState,
  ownProps: unknown,
  callback: (editor: Editor, ownProps) => unknown,
): unknown => {
  return callback(state.editor, ownProps);
};
