export interface ControlPanelProps {
  onChangeLang: () => void;
  onSave: () => void;
  onUndo: () => void;
  onRedo: () => void;
  onAddText: () => void;
  onAddImage: (src: string | ArrayBuffer) => void;
  onAddShape: (
    type:
      | "triangle"
      | "circle"
      | "hexagon"
      | "pentagon"
      | "rectangle"
      | "heart"
      | "star",
  ) => void;
}

export interface ShapeControlProps {
  x: number;
  y: number;
  w: number;
  h: number;
  angle: number;
  color: string;
  onChangeDimensions: (e: {
    x: number;
    y: number;
    w: number;
    h: number;
    angle: number;
  }) => void;
  onChangeColor: (e: { color: string }) => void;
  onChangeColorOver: (e: { color: string }) => void;
  font: string;
  size: number;
  bold: boolean;
  italic: boolean;
  underline: boolean;
  alignment: "left" | "center" | "right";
  onChangeFont: (e: {
    font: string;
    size: number;
    bold: boolean;
    italic: boolean;
    underline: boolean;
    alignment: "left" | "center" | "right";
  }) => void;
  onCopyItem: () => void;
  onDeleteItem: () => void;
  displayFontPanel: boolean;
}

export interface LayerControlProps {
  key: string;
  name: string;
  image: JSX.Element;
  onDelete: () => void;
  onRename: (newName: string) => void;
}

export interface LayerControlPanelProps {
  layers: LayerControlProps[];
  onChangeOrder: (keys: string[]) => void;
}

export interface SlideControlPanelProps {
  key: string;
  renderSlide: () => JSX.Element;
  onChangeSlide: () => void;
  onDeleteSlide: () => void;
  isSelected: boolean;
}

export interface SlidesControlPanelProps {
  items: SlideControlPanelProps[];
  onChangeOrder: (ids: string[]) => void;
  onChangeSlide: (id: string) => void;
  onCreateSlide: () => void;
}

export abstract class EditorUIFactory {
  abstract ControlPanel(props: ControlPanelProps): JSX.Element;

  abstract ShapeControl(props: ShapeControlProps): JSX.Element;

  abstract LayersPanel(props: LayerControlPanelProps): JSX.Element;

  abstract SlidesPanel(props: SlidesControlPanelProps): JSX.Element;
}
