import { BaseCommand, registerCommand } from "./History";
import { Canvas, Slide } from "./Canvas";

const CHANGE_SLIDE_ORDER_COMMAND = "CHANGE_SLIDE_ORDER_COMMAND";

class ChangeSlideOrderCommand extends BaseCommand {
  type: typeof CHANGE_SLIDE_ORDER_COMMAND;
  beforeChange: string[];
  afterChange: string[];
}

export interface ChangeSlideOrderRequest {
  newOrderIds: string[];
}

export const makeChangeSlideOrderCommand = (
  e: ChangeSlideOrderRequest,
  items: Slide[],
): ChangeSlideOrderCommand => {
  return {
    type: CHANGE_SLIDE_ORDER_COMMAND,
    isFantom: false,
    beforeChange: items.map((i) => i.id),
    afterChange: e.newOrderIds,
  };
};

registerCommand(CHANGE_SLIDE_ORDER_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas, slides): void => {
    const command = cmd as ChangeSlideOrderCommand;

    const order: Map<string, Slide> = new Map();

    const items = slides.splice(0, slides.length);
    items.forEach((s) => order.set(s.id, s));

    for (const id of command.beforeChange) {
      slides.push(order.get(id));
    }
  },
  execute: (cmd: BaseCommand, ctx: Canvas, slides): void => {
    const command = cmd as ChangeSlideOrderCommand;

    const order: Map<string, Slide> = new Map();

    const items = slides.splice(0, slides.length);
    items.forEach((s) => order.set(s.id, s));

    for (const id of command.afterChange) {
      slides.push(order.get(id));
    }
  },
});
