import {
  CommonUIFactory,
  UIContainerProps,
  UIGridProps,
} from "../application/ui/CommonUIFactory";
import { MuiCommonFactoryImpl } from "./mui/MuiCommonFactoryImpl";

const factory: CommonUIFactory = new MuiCommonFactoryImpl();

export class UIFactory {
  public static Grid(props: UIGridProps): JSX.Element {
    return factory.Grid(props);
  }

  public static Container(props: UIContainerProps): JSX.Element {
    return factory.Container(props);
  }
}
