import { History } from "./History";
import { Canvas, createSlide, Slide } from "./Canvas";
import { Buffer, createBuffer } from "./Buffer";

export class Editor {
  slides: Slide[];
  canvas: Canvas;
  buffer: Buffer;
  history: History;
}

export const createEditor = (): Editor => {
  const firstSlide = createSlide();

  return {
    slides: [firstSlide],
    canvas: {
      standardWidth: 1600,
      standardHeight: 900,
      width: 0,
      height: 0,
      selectedItemId: null,
      ...firstSlide,
    },
    buffer: createBuffer(),
    history: {
      past: [],
      future: [],
    },
  };
};
