import * as React from "react";
import { Theme } from "@material-ui/core";
import { UIContainerProps } from "../../application/ui/CommonUIFactory";
import "./styles/common.scss";
import { WithStyles, withStyles } from "@material-ui/styles";

const styles = (t: Theme) => ({
  container: {
    padding: t.spacing(1),
  },
});

interface Props extends UIContainerProps, WithStyles<typeof styles> {}

class MuiContainerFactory extends React.Component<Props, unknown> {
  render(): JSX.Element {
    return (
      <div
        className={[
          this.props.className,
          this.props.classes.container,
          "mui-container",
        ].join(" ")}
        style={
          !this.props.withIndent
            ? { ...this.props.style, padding: 0 }
            : { ...this.props.style }
        }
      >
        {this.props.children}
      </div>
    );
  }
}

export default withStyles(styles)(MuiContainerFactory);
