import * as React from "react";
import { Drawable, ItemTypes } from "../model/Drawable";

export interface DrawableProps extends Drawable {
  onChangeText: (e: string) => void;
  onClick: () => void;
  isActive: boolean;
}

const Image = (props: DrawableProps): JSX.Element => {
  const { onClick } = props;

  const { x, y, w, h, angle } = props.dimensions;

  const { backgroundImage } = props.appearance;

  return (
    <div
      style={{
        position: "absolute",
        width: w,
        height: h,
        left: x,
        top: y,
        transform: `rotate(${angle}deg)`,
        zIndex: 2,
      }}
      onClick={() => onClick()}
    >
      <img width={w} height={h} src={`${backgroundImage}`} alt="image" />
    </div>
  );
};

const Text = (props: DrawableProps): JSX.Element => {
  const { onChangeText, onClick, isActive } = props;

  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  const { font, size, value, bold, italic, underline, alignment } = props.text;

  return (
    <div
      style={{
        position: "absolute",
        width: isActive ? w - 10 : w,
        height: isActive ? h - 10 : h,
        left: isActive ? x + 5 : x,
        top: isActive ? y + 5 : y,
        transform: `rotate(${angle}deg)`,
        zIndex: isActive ? 3 : 2,
        fontSize: size,
        overflow: "hidden",
      }}
      onClick={() => onClick()}
    >
      <textarea
        style={{
          position: "absolute",
          fontFamily: font,
          fontSize: "1em",
          fontWeight: bold ? "bold" : "normal",
          textDecoration: underline ? "underline" : "none",
          fontStyle: italic ? "italic" : "normal",
          top: 0,
          left: 0,
          width: isActive ? w - 10 : w,
          height: isActive ? h - 10 : h,
          textAlign: alignment,

          resize: "none",
          border: "none",
          lineHeight: "100%",
          color,
          overflow: "hidden",
          backgroundColor: "inherit",
          padding: 0,
        }}
        value={value}
        onChange={(e) => onChangeText(e.target.value)}
      />
    </div>
  );
};

const Ellipse = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        position: "absolute",
        left: x,
        top: y,
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <ellipse
        style={{ pointerEvents: "auto" }}
        onClick={props.onClick}
        rx={w / 2}
        ry={h / 2}
        cx={w / 2}
        cy={h / 2}
        fill={color}
      />
    </svg>
  );
};

const Rectangle = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <rect
        style={{ pointerEvents: "auto" }}
        onClick={props.onClick}
        width={w}
        height={h}
        fill={color}
      />
    </svg>
  );
};

const Triangle = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <polygon
        style={{ pointerEvents: "auto" }}
        onClick={props.onClick}
        points={`0 ${h}, ${w / 2} 0, ${w} ${h}`}
        width={w}
        height={h}
        fill={color}
      />
    </svg>
  );
};

const Star = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <polygon
        style={{ pointerEvents: "auto" }}
        points={`${0.5 * w},0 ${0.25 * w},${h} ${w},${0.3 * h} 0,${0.3 * h} ${
          0.75 * w
        },${h}`}
        fill={color}
        onClick={props.onClick}
      />
    </svg>
  );
};

const Hexagon = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <polygon
        style={{ pointerEvents: "auto" }}
        points={`${w / 3},0 ${(2 * w) / 3},0 ${w},${h / 3} ${w},${
          (2 * h) / 3
        } ${(2 * w) / 3},${h} ${w / 3},${h} 0,${(2 * h) / 3} 0,${h / 3}`}
        fill={color}
        onClick={props.onClick}
      />
    </svg>
  );
};

const Hearth = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
      fillRule="evenodd"
      clipRule="evenodd"
      fill={color}
      viewBox="0 0 24 24"
    >
      <path
        style={{ pointerEvents: "auto" }}
        onClick={props.onClick}
        d="M12 4.248c-3.148-5.402-12-3.825-12 2.944 0 4.661 5.571 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-6.792-8.875-8.306-12-2.944z"
      />
    </svg>
  );
};

const Pentagon = (props: DrawableProps): JSX.Element => {
  const { x, y, w, h, angle } = props.dimensions;

  const { color } = props.appearance;

  return (
    <svg
      style={{
        left: x,
        top: y,
        position: "absolute",
        transform: `rotate(${angle}deg)`,
        pointerEvents: "none",
        zIndex: 2,
      }}
      width={w}
      height={h}
    >
      <polygon
        style={{ pointerEvents: "auto" }}
        points={`${w / 2},0 0,${0.25 * h} ${0.25 * w},${h} ${
          w - 0.25 * w
        },${h} ${w},${0.25 * h}`}
        fill={color}
        onClick={props.onClick}
      />
    </svg>
  );
};

const DrawableFactory = (props: DrawableProps): JSX.Element => {
  const { type } = props;
  return (
    <React.Fragment>
      {type === ItemTypes.CIRCLE && <Ellipse {...props} />}
      {type === ItemTypes.RECTANGLE && <Rectangle {...props} />}
      {type === ItemTypes.TRIANGLE && <Triangle {...props} />}
      {type === ItemTypes.STAR && <Star {...props} />}
      {type === ItemTypes.HEXAGON && <Hexagon {...props} />}
      {type === ItemTypes.HEARTH && <Hearth {...props} />}
      {type === ItemTypes.PENTAGON && <Pentagon {...props} />}
      {type === ItemTypes.TEXT && <Text {...props} />}
      {type === ItemTypes.IMAGE && <Image {...props} />}
    </React.Fragment>
  );
};

export default DrawableFactory;
