import { BaseCommand, registerCommand } from "./History";
import { Drawable } from "./Drawable";
import { Canvas } from "./Canvas";

const DELETE_ITEM_COMMAND = "DELETE_ITEM_COMMAND";

interface DeleteItemCommand extends BaseCommand {
  type: typeof DELETE_ITEM_COMMAND;
  beforeDelete: Drawable[];
  afterDelete: Drawable[];
}

export const makeDeleteItemCommand = (e: {
  before: Drawable[];
  after: Drawable[];
}): DeleteItemCommand => {
  return {
    isFantom: false,
    type: DELETE_ITEM_COMMAND,
    beforeDelete: e.before,
    afterDelete: e.after,
  };
};

registerCommand(DELETE_ITEM_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as DeleteItemCommand;
    ctx.items = command.beforeDelete;
  },
  execute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as DeleteItemCommand;
    ctx.items = command.afterDelete;
    ctx.selectedItemId = null;
  },
});
