import * as React from "react";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { connect } from "react-redux";
import { Drawable } from "../model/Drawable";
import { UIFactory } from "../../infrastructure/UIFactory";
import DrawableFactory from "./DrawableFactory";

interface LayersPanelProps extends DispatchProps {
  items: Drawable[];
}

class LayersPanel extends React.Component<LayersPanelProps, unknown> {
  constructor(props: LayersPanelProps) {
    super(props);
  }

  render(): JSX.Element {
    const { onUpdateItem, onChangeOrder, onDeleteItem, items } = this.props;

    return (
      <UIFactory.LayersControlPanel
        onChangeOrder={(ids) => onChangeOrder({ newOrderIds: ids })}
        layers={items.map((i) => ({
          key: i.id,
          name: i.name,
          image: (
            <DrawableFactory
              onClick={() => console.log()}
              id={i.id}
              type={i.type}
              name={i.name}
              dimensions={{ x: 0, y: 0, w: 30, h: 30, angle: 0 }}
              appearance={{
                color: i.appearance.color,
                backgroundImage: i.appearance.backgroundImage,
              }}
              text={{
                bold: i.text.bold,
                italic: i.text.italic,
                underline: i.text.underline,
                value: "T",
                alignment: "center",
                font: i.text.font,
                size: 30,
              }}
              onChangeText={() => console.log()}
              isActive={false}
            />
          ),
          onDelete: () => onDeleteItem({ id: i.id }),
          onRename: (name) => onUpdateItem({ id: i.id, name }),
        }))}
      />
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    items: editor.canvas.items,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(LayersPanel);
