import { BaseCommand, registerCommand } from "./History";
import { Drawable } from "./Drawable";
import { Canvas } from "./Canvas";

export const CHANGE_SHAPE_ORDER_COMMAND = "CHANGE_SHAPE_ORDER_COMMAND";

interface ChangeShapeOrderCommand extends BaseCommand {
  type: typeof CHANGE_SHAPE_ORDER_COMMAND;
  beforeChange: Drawable[];
  afterChange: Drawable[];
}

export interface ChangeOrderPayload {
  newOrderIds: string[];
}

export const createChangeOrderCommand = (
  e: ChangeOrderPayload,
  ctx: Canvas,
): ChangeShapeOrderCommand => {
  const beforeChange = JSON.parse(JSON.stringify(ctx.items));

  let afterChange = JSON.parse(JSON.stringify(ctx.items));

  const mapped: Record<string, Drawable> = {};

  afterChange.forEach((s) => {
    mapped[s.id] = s;
  });

  afterChange = e.newOrderIds.map((k) => mapped[k]);

  return {
    isFantom: false,
    type: CHANGE_SHAPE_ORDER_COMMAND,
    beforeChange,
    afterChange,
  };
};

registerCommand(CHANGE_SHAPE_ORDER_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as ChangeShapeOrderCommand;
    ctx.items = command.beforeChange;
  },
  execute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as ChangeShapeOrderCommand;
    ctx.items = command.afterChange;
  },
});
