import * as React from "react";
import { UIFactory } from "../../infrastructure/UIFactory";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { connect } from "react-redux";
import { Slide } from "../model/Canvas";
import SlidePreview from "./SlidePreview";

interface SlidesPanelProps extends DispatchProps {
  slides: Slide[];
  currentSlideId: string;
}

class SlidesPanel extends React.Component<SlidesPanelProps, unknown> {
  render(): JSX.Element {
    const {
      currentSlideId,
      slides,
      onDeleteSlide,
      onCreateSlide,
      onChangeSlide,
      onChangeSlideOrder,
    } = this.props;

    return (
      <UIFactory.SlidesPanel
        items={slides.map((s) => ({
          key: s.id,
          onDeleteSlide: () => onDeleteSlide({ id: s.id }),
          onChangeSlide: () => {
            if (currentSlideId !== s.id) {
              onChangeSlide({ id: s.id });
            }
          },
          renderSlide: () => <SlidePreview slide={s} />,
          isSelected: s.id === currentSlideId,
        }))}
        onCreateSlide={() => onCreateSlide()}
        onChangeOrder={(e) => onChangeSlideOrder({ newOrderIds: e })}
        onChangeSlide={() => console.log()}
      />
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    currentSlideId: editor.canvas.id,
    slides: editor.slides,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(SlidesPanel);
