export const countAngleBetweenVectors = (
  { x: x1, y: y1 }: { x: number; y: number },
  { x: x2, y: y2 }: { x: number; y: number },
): number => {
  const dot = x1 * x2 + y1 * y2;
  const det = x1 * y2 - y1 * x2;
  const angle = (Math.atan2(det, dot) / Math.PI) * 180;
  return (angle + 360) % 360;
};

export const degToRadian = (deg: number): number => (deg * Math.PI) / 180;

export const cos = (deg: number): number => Math.cos(degToRadian(deg));

export const sin = (deg: number): number => Math.sin(degToRadian(deg));

const calcWidthAndDeltaWidth = (width, deltaW, minWidth) => {
  const expectedWidth = width + deltaW;
  if (expectedWidth > minWidth) {
    width = expectedWidth;
  } else {
    deltaW = minWidth - width;
    width = minWidth;
  }
  return { width, deltaW };
};

const countHeightAndDeltaHeight = (height, deltaH, minHeight) => {
  const expectedHeight = height + deltaH;
  if (expectedHeight > minHeight) {
    height = expectedHeight;
  } else {
    deltaH = minHeight - height;
    height = minHeight;
  }
  return { height, deltaH };
};

export enum SurfacePointers {
  RIGHT = "r",
  TOP_RIGHT = "tr",
  BOTTOM_RIGHT = "br",
  BOTTOM = "b",
  BOTTOM_LEFT = "bl",
  LEFT = "l",
  TOP_LEFT = "tl",
  TOP = "t",
}

export interface Location {
  top: number;
  left: number;
  width: number;
  height: number;
  rotateAngle: number;
}

export const calcNewLocation = (
  type: SurfacePointers,
  rect: {
    width: number;
    height: number;
    centerX: number;
    centerY: number;
    rotateAngle: number;
  },
  deltaW: number,
  deltaH: number,
  ratio: number | false,
  minWidth: number,
  minHeight: number,
): Location => {
  let { width, height, centerX, centerY } = rect;

  const { rotateAngle } = rect;

  const widthFlag = width < 0 ? -1 : 1;

  const heightFlag = height < 0 ? -1 : 1;

  width = Math.abs(width);

  height = Math.abs(height);

  switch (type) {
    case SurfacePointers.RIGHT: {
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      if (ratio) {
        deltaH = deltaW / ratio;
        height = width / ratio;
        centerX +=
          (deltaW / 2) * cos(rotateAngle) - (deltaH / 2) * sin(rotateAngle);
        centerY +=
          (deltaW / 2) * sin(rotateAngle) + (deltaH / 2) * cos(rotateAngle);
      } else {
        centerX += (deltaW / 2) * cos(rotateAngle);
        centerY += (deltaW / 2) * sin(rotateAngle);
      }
      break;
    }
    case SurfacePointers.TOP_RIGHT: {
      deltaH = -deltaH;
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        deltaW = deltaH * ratio;
        width = height * ratio;
      }
      centerX +=
        (deltaW / 2) * cos(rotateAngle) + (deltaH / 2) * sin(rotateAngle);
      centerY +=
        (deltaW / 2) * sin(rotateAngle) - (deltaH / 2) * cos(rotateAngle);
      break;
    }
    case SurfacePointers.BOTTOM_RIGHT: {
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        deltaW = deltaH * ratio;
        width = height * ratio;
      }
      centerX +=
        (deltaW / 2) * cos(rotateAngle) - (deltaH / 2) * sin(rotateAngle);
      centerY +=
        (deltaW / 2) * sin(rotateAngle) + (deltaH / 2) * cos(rotateAngle);
      break;
    }
    case SurfacePointers.BOTTOM: {
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        deltaW = deltaH * ratio;
        width = height * ratio;
        centerX +=
          (deltaW / 2) * cos(rotateAngle) - (deltaH / 2) * sin(rotateAngle);
        centerY +=
          (deltaW / 2) * sin(rotateAngle) + (deltaH / 2) * cos(rotateAngle);
      } else {
        centerX -= (deltaH / 2) * sin(rotateAngle);
        centerY += (deltaH / 2) * cos(rotateAngle);
      }
      break;
    }
    case SurfacePointers.BOTTOM_LEFT: {
      deltaW = -deltaW;
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        height = width / ratio;
        deltaH = deltaW / ratio;
      }
      centerX -=
        (deltaW / 2) * cos(rotateAngle) + (deltaH / 2) * sin(rotateAngle);
      centerY -=
        (deltaW / 2) * sin(rotateAngle) - (deltaH / 2) * cos(rotateAngle);
      break;
    }
    case SurfacePointers.LEFT: {
      deltaW = -deltaW;
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      if (ratio) {
        height = width / ratio;
        deltaH = deltaW / ratio;
        centerX -=
          (deltaW / 2) * cos(rotateAngle) + (deltaH / 2) * sin(rotateAngle);
        centerY -=
          (deltaW / 2) * sin(rotateAngle) - (deltaH / 2) * cos(rotateAngle);
      } else {
        centerX -= (deltaW / 2) * cos(rotateAngle);
        centerY -= (deltaW / 2) * sin(rotateAngle);
      }
      break;
    }
    case SurfacePointers.TOP_LEFT: {
      deltaW = -deltaW;
      deltaH = -deltaH;
      const widthAndDeltaW = calcWidthAndDeltaWidth(width, deltaW, minWidth);
      width = widthAndDeltaW.width;
      deltaW = widthAndDeltaW.deltaW;
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        width = height * ratio;
        deltaW = deltaH * ratio;
      }
      centerX -=
        (deltaW / 2) * cos(rotateAngle) - (deltaH / 2) * sin(rotateAngle);
      centerY -=
        (deltaW / 2) * sin(rotateAngle) + (deltaH / 2) * cos(rotateAngle);
      break;
    }
    case SurfacePointers.TOP: {
      deltaH = -deltaH;
      const heightAndDeltaH = countHeightAndDeltaHeight(
        height,
        deltaH,
        minHeight,
      );
      height = heightAndDeltaH.height;
      deltaH = heightAndDeltaH.deltaH;
      if (ratio) {
        width = height * ratio;
        deltaW = deltaH * ratio;
        centerX +=
          (deltaW / 2) * cos(rotateAngle) + (deltaH / 2) * sin(rotateAngle);
        centerY +=
          (deltaW / 2) * sin(rotateAngle) - (deltaH / 2) * cos(rotateAngle);
      } else {
        centerX += (deltaH / 2) * sin(rotateAngle);
        centerY -= (deltaH / 2) * cos(rotateAngle);
      }
      break;
    }
  }

  return {
    top: centerY - height / 2,
    left: centerX - width / 2,
    width: width * widthFlag,
    height: height * heightFlag,
    rotateAngle,
  };
};
