import * as React from "react";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import { SlidesControlPanelProps } from "../../application/ui/EditorUIFactory";
import { Button, Divider, IconButton, Typography } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import "./styles/slide-control-panel.scss";

interface MuiSlidesControlPanelState {
  w: number;
  h: number;
}

class MuiSlidesControlPanel extends React.Component<
  SlidesControlPanelProps,
  MuiSlidesControlPanelState
> {
  private mockItem: HTMLElement | null;

  private readonly onResize: () => void;

  constructor(props: SlidesControlPanelProps) {
    super(props);

    this.state = {
      w: 0,
      h: 0,
    };

    this.onResize = this.resizePreview.bind(this);
  }

  componentDidMount(): void {
    window.addEventListener("resize", this.onResize);
    this.onResize();
  }

  componentWillUnmount(): void {
    window.removeEventListener("resize", this.onResize);
  }

  resizePreview(): void {
    const { offsetWidth } = this.mockItem;

    this.setState({
      w: offsetWidth,
      h: (offsetWidth / 16) * 9,
    });
  }

  render(): JSX.Element {
    const { onCreateSlide, onChangeOrder, items } = this.props;

    const { w, h } = this.state;

    return (
      <div className="mui-slides-panel-container">
        <div className="mui-slides-panel-add-slide-button">
          <Button
            size="small"
            color="warning"
            variant="outlined"
            fullWidth
            startIcon={<ControlPointIcon />}
            onClick={onCreateSlide}
          >
            New
          </Button>
        </div>
        <Divider />
        <div style={{ paddingTop: 12 }}>
          <div ref={(e) => (this.mockItem = e)} />
          <DragDropContext
            onDragEnd={(e) => {
              if (!e.destination) {
                return;
              }

              const keys = items.map((s) => s.key);

              if (keys.indexOf(e.draggableId) === e.destination.index) {
                return;
              }

              keys.splice(keys.indexOf(e.draggableId), 1);
              keys.splice(e.destination.index, 0, e.draggableId);

              onChangeOrder(keys);
            }}
          >
            <Droppable droppableId="mui-droppable-area-slides">
              {(provided) => (
                <div {...provided.droppableProps} ref={provided.innerRef}>
                  {w != 0 &&
                    h != 0 &&
                    items.map((v, i, a) => (
                      <Draggable key={v.key} draggableId={v.key} index={i}>
                        {(provided) => (
                          <div
                            className="mui-slides-panel-item"
                            style={{
                              width: w,
                              height: h,
                            }}
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <div
                              onClick={() => v.onChangeSlide()}
                              style={{
                                width: w,
                                height: h,
                              }}
                            >
                              <div
                                style={{
                                  width: w,
                                  height: h,
                                  pointerEvents: "none",
                                  userSelect: "none",
                                }}
                              >
                                {v.renderSlide()}
                              </div>
                            </div>
                            <div
                              className="mui-slides-panel-item-number"
                              style={{
                                borderColor: v.isSelected ? "#919191" : null,
                                backgroundColor: v.isSelected
                                  ? "#f5f5f5"
                                  : null,
                              }}
                            >
                              <Typography variant="subtitle2">
                                {i + 1}
                              </Typography>
                            </div>
                            <div className="mui-slides-panel-item-delete-button">
                              <IconButton
                                size="small"
                                color="error"
                                onClick={v.onDeleteSlide}
                                disabled={a.length === 1}
                              >
                                <DeleteIcon fontSize="small" />
                              </IconButton>
                            </div>
                          </div>
                        )}
                      </Draggable>
                    ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        </div>
      </div>
    );
  }
}

export default MuiSlidesControlPanel;
