interface RotatedRect {
  w: number;
  h: number;
  x: number;
  y: number;
  angle: number;
}

interface WrapperRect {
  w1: number;
  h1: number;
  x1: number;
  y1: number;
  angle: number;
}

export const calcWrapperBounds = (rect: RotatedRect): WrapperRect => {
  const { x, y, w, h, angle } = rect;

  const cos = (deg: number): number => Math.cos((deg * Math.PI) / 180);

  const sin = (deg: number): number => Math.sin((deg * Math.PI) / 180);

  const abs = (v) => Math.abs(v);

  const h1 = h * abs(cos(angle)) + w * abs(sin(angle)),
    w1 = h * abs(sin(angle)) + w * abs(cos(angle)),
    x1 = x + w / 2 - w1 / 2,
    y1 = y + h / 2 - h1 / 2;

  return {
    x1,
    y1,
    w1,
    h1,
    angle: rect.angle,
  };
};

export const calcRectBounds = (wrapper: WrapperRect): RotatedRect => {
  const { x1, y1, w1, h1, angle } = wrapper;

  const cos = (deg: number): number => Math.cos((deg * Math.PI) / 180),
    sin = (deg: number): number => Math.sin((deg * Math.PI) / 180),
    abs = (v) => Math.abs(v),
    pow = (x: number, y: number) => Math.pow(x, y);

  const c1 = abs(cos(angle)),
    c2 = abs(sin(angle));

  const h = abs((w1 * c2 - h1 * c1) / (pow(c2, 2) + pow(c1, 2))),
    w = abs((h1 * c2 - w1 * c1) / (pow(c2, 2) - pow(c1, 2))),
    x = x1 - w / 2 + w1 / 2,
    y = y1 - h / 2 + h1 / 2;

  return {
    x,
    y,
    w,
    h,
    angle,
  };
};
