import * as React from "react";
import { useEffect, useState } from "react";
import { Divider, IconButton, TextField, Typography } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import {
  LayerControlPanelProps,
  LayerControlProps,
} from "../../application/ui/EditorUIFactory";
import "./styles/layers-control-panel.scss";

const MuiLayerControlPanel = ({
  name,
  image,
  onDelete,
  onRename,
}: LayerControlProps): JSX.Element => {
  const [rName, setRName] = useState(name);

  useEffect(() => {
    setRName(name);
  }, [name]);

  const onRenamedRName = () => {
    if (name !== rName) {
      onRename(rName);
    }
  };

  return (
    <div className="mui-layers-panel-layer">
      <div className="mui-layers-panel-layer-container">
        <div className="mui-layers-panel-layer-image">{image}</div>
        <div className="mui-layers-panel-layer-name">
          <TextField
            fullWidth
            size="small"
            style={{ height: 30 }}
            value={rName}
            onBlur={onRenamedRName}
            onChange={(e) => setRName(e.target.value)}
          />
        </div>
        <div>
          <IconButton size="small" onClick={onDelete}>
            <Delete fontSize="small" />
          </IconButton>
        </div>
      </div>
      <div className="mui-layers-panel-layer-divider">
        <Divider />
      </div>
    </div>
  );
};

const MuiLayersControlPanelFactory = ({
  layers,
  onChangeOrder,
}: LayerControlPanelProps): JSX.Element => {
  const onDragOver = (e) => {
    if (!e.destination) {
      return;
    }

    const keys = layers.map((l) => l.key);

    if (keys.indexOf(e.draggableId) === e.destination.index) {
      return;
    }

    keys.splice(keys.indexOf(e.draggableId), 1);
    keys.splice(e.destination.index, 0, e.draggableId);

    onChangeOrder(keys);
  };

  return (
    <div>
      {layers.length === 0 && (
        <div style={{ textAlign: "center", paddingTop: 20 }}>
          <div style={{ paddingTop: 10 }}>
            <Typography>There are no items yet</Typography>
          </div>
        </div>
      )}
      <DragDropContext onDragEnd={(e) => onDragOver(e)}>
        <Droppable droppableId="mui-droppable-area">
          {(provided) => (
            <div
              className="mui-layers-panel-container"
              {...provided.droppableProps}
              ref={provided.innerRef}
            >
              {layers.map((l, i) => (
                <Draggable key={l.key} draggableId={l.key} index={i}>
                  {(provided) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                    >
                      <MuiLayerControlPanel
                        key={l.key}
                        name={l.name}
                        image={l.image}
                        onDelete={l.onDelete}
                        onRename={l.onRename}
                      />
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default MuiLayersControlPanelFactory;
