import * as React from "react";
import { Grid, Hidden } from "@material-ui/core";
import { UIGridProps } from "../../application/ui/CommonUIFactory";

const MuiGrid = ({
  container,
  xsDown,
  xsUp,
  smDown,
  smUp,
  mdDown,
  mdUp,
  lgDown,
  lgUp,
  xlDown,
  xlUp,
  xs,
  sm,
  md,
  lg,
  xl,
  children,
  style,
  alignItems,
}: UIGridProps): JSX.Element => {
  return container ? (
    <Grid style={{ ...style }} container spacing={0} alignItems={alignItems}>
      {children}
    </Grid>
  ) : (
    <Hidden
      xsDown={xsDown}
      xsUp={xsUp}
      smDown={smDown}
      smUp={smUp}
      mdDown={mdDown}
      mdUp={mdUp}
      lgDown={lgDown}
      lgUp={lgUp}
      xlDown={xlDown}
      xlUp={xlUp}
    >
      <Grid style={{ ...style }} item xs={xs} sm={sm} md={md} lg={lg} xl={xl}>
        {children}
      </Grid>
    </Hidden>
  );
};

export default MuiGrid;
