import * as React from "react";
import {
  Button,
  ClickAwayListener,
  Divider,
  Grid,
  Grow,
  Paper,
  Popper,
  Tooltip,
} from "@material-ui/core";
import SaveIcon from "@material-ui/icons/Save";
import UndoIcon from "@material-ui/icons/Undo";
import RedoIcon from "@material-ui/icons/Redo";
import RadioButtonUncheckedIcon from "@material-ui/icons/RadioButtonUnchecked";
import ChangeHistoryIcon from "@material-ui/icons/ChangeHistory";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import TextFieldsIcon from "@material-ui/icons/TextFields";
import ImageIcon from "@material-ui/icons/Image";
import LanguageIcon from "@material-ui/icons/Language";
import StarIcon from "@material-ui/icons/StarBorderOutlined";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import { grey } from "@material-ui/core/colors";
import { ControlPanelProps } from "../../application/ui/EditorUIFactory";
import { ChangeEvent } from "react";
import "../../../common/infrastructure/mui/styles/common.scss";
import "./styles/control-panel.scss";

const Hexagon = (): JSX.Element => {
  return (
    <svg width="18px" height="18px" viewBox="0 0 200 200">
      <polygon
        style={{ fill: grey[700] }}
        points="136,188 63,188 11,136 11,63 63,11 136,11 188,63 188,136"
      />
    </svg>
  );
};

const Pentagon = (): JSX.Element => {
  return (
    <svg
      style={{ transform: "rotate(180deg)" }}
      width="16"
      height="16"
      viewBox="-1 0 101 100"
    >
      <path d="M20,0 L80,0 L100,60 L50,100 L0,60z" fill={grey[700]} />
    </svg>
  );
};

const MuiControlPanel = ({
  onSave,
  onUndo,
  onRedo,
  onAddText,
  onAddImage,
  onAddShape,
  onChangeLang,
}: ControlPanelProps): JSX.Element => {
  const [isShapeListVisible, setIsShapeListVisible] = React.useState(false);

  const [
    shapeAnchor,
    setShapeAnchor,
  ] = React.useState<HTMLButtonElement | null>(null);

  const onPickImage = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files.length === 0) {
      return;
    }

    const reader = new FileReader();
    reader.addEventListener("loadend", () => {
      onAddImage(reader.result);
      e.target.value = "";
    });

    reader.readAsDataURL(e.target.files[0]);
  };

  return (
    <div>
      <Grid container>
        <Grid item>
          <Tooltip title="Change Language">
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              onClick={onChangeLang}
            >
              <LanguageIcon fontSize="small" />
            </Button>
          </Tooltip>
        </Grid>
        <Divider orientation="vertical" flexItem style={{ margin: "0 5px" }} />
        <Grid item>
          <Tooltip title="Copy To Clipboard">
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              onClick={onSave}
            >
              <SaveIcon fontSize="small" />
            </Button>
          </Tooltip>
        </Grid>
        <Grid item>
          <Tooltip title="Undo">
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              onClick={onUndo}
            >
              <UndoIcon fontSize="small" />
            </Button>
          </Tooltip>
        </Grid>
        <Grid item>
          <Tooltip title="Redo">
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              onClick={onRedo}
            >
              <RedoIcon fontSize="small" />
            </Button>
          </Tooltip>
        </Grid>
        <Divider orientation="vertical" flexItem style={{ margin: "0 5px" }} />
        <Grid item>
          <Tooltip title="Add Text Field">
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              onClick={onAddText}
            >
              <TextFieldsIcon fontSize="small" />
            </Button>
          </Tooltip>
        </Grid>
        <Grid item>
          <label htmlFor="image-button">
            <input
              style={{ display: "none" }}
              accept="image/*"
              id="image-button"
              type="file"
              onChange={(e) => onPickImage(e)}
            />
            <Button
              className="mui-control-panel-menu-button"
              size="small"
              component="span"
            >
              <ImageIcon fontSize="small" />
            </Button>
          </label>
        </Grid>
        <Divider orientation="vertical" flexItem style={{ margin: "0 5px" }} />
        <Grid item>
          <Button
            className="mui-control-panel-menu-button"
            size="small"
            endIcon={<ExpandMoreIcon fontSize="small" />}
            onClick={(e) => {
              setShapeAnchor(e.currentTarget);
              setIsShapeListVisible(true);
            }}
          >
            <ChangeHistoryIcon fontSize="small" />
          </Button>
        </Grid>
        <Divider orientation="vertical" flexItem style={{ margin: "0 5px" }} />
      </Grid>
      <Popper
        style={{
          zIndex: 2147483647,
        }}
        open={isShapeListVisible}
        anchorEl={shapeAnchor}
        transition
        disablePortal
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin:
                placement === "bottom" ? "center top" : "center bottom",
            }}
          >
            <Paper elevation={0} className="mui-container">
              <ClickAwayListener
                onClickAway={() => setIsShapeListVisible(false)}
              >
                <div style={{ padding: "5px" }}>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("triangle")}
                  >
                    <ChangeHistoryIcon fontSize="small" />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("circle")}
                  >
                    <RadioButtonUncheckedIcon fontSize="small" />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("hexagon")}
                  >
                    <Hexagon />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("rectangle")}
                  >
                    <CheckBoxOutlineBlankIcon fontSize="small" />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("star")}
                  >
                    <StarIcon fontSize="small" />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("pentagon")}
                  >
                    <Pentagon />
                  </Button>
                  <Button
                    className="mui-control-panel-menu-button"
                    size="small"
                    onClick={() => onAddShape("heart")}
                  >
                    <FavoriteBorderIcon fontSize="small" />
                  </Button>
                </div>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </div>
  );
};

export default MuiControlPanel;
