import { BaseCommand, registerCommand } from "./History";
import { Canvas, createSlide, Slide } from "./Canvas";

const CREATE_SLIDE_COMMAND = "CREATE_SLIDE_COMMAND";

class CreateSlideCommand extends BaseCommand {
  type: typeof CREATE_SLIDE_COMMAND;
  before: Slide[];
  after: Slide[];
  previousSlideId: string;
  nextSlideId: string;
}

export const makeCreateSlideCommand = (
  ctx: Canvas,
  slides: Slide[],
): CreateSlideCommand => {
  const before = JSON.parse(JSON.stringify(slides));

  let after: Slide[] = JSON.parse(JSON.stringify(slides));

  const newSlide = createSlide();

  after = after.flatMap((s) => {
    if (s.id === ctx.id) {
      return [s, newSlide];
    }

    return [s];
  });

  return {
    isFantom: false,
    type: CREATE_SLIDE_COMMAND,
    before,
    after,
    previousSlideId: ctx.id,
    nextSlideId: newSlide.id,
  };
};

registerCommand(CREATE_SLIDE_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as CreateSlideCommand;
    items.splice(0, items.length);
    command.before.forEach((i) => items.push(i));

    if (command.nextSlideId === ctx.id) {
      ctx.id = command.previousSlideId;
      ctx.selectedItemId = null;
      ctx.items = items.find((s) => s.id === command.previousSlideId).items;
    }
  },
  execute: (cmd: BaseCommand, ctx: Canvas, items: Slide[]): void => {
    const command = cmd as CreateSlideCommand;
    items.splice(0, items.length);
    command.after.forEach((i) => items.push(i));
    ctx.id = command.nextSlideId;
    ctx.selectedItemId = null;
    ctx.items = [];
  },
});
