import { Drawable } from "./Drawable";

export interface Buffer {
  value: Drawable | null;
}

export const createBuffer = (): Buffer => ({
  value: null,
});

export interface CopyItemToBufferPayload {
  item: Drawable;
}

export const copyItemToBuffer = (
  e: CopyItemToBufferPayload,
  buffer: Buffer,
): void => {
  buffer.value = e.item;
};
