import { Canvas, Slide } from "./Canvas";

export class BaseCommand {
  type: string;
  isFantom: boolean;
}

export class History {
  past: BaseCommand[];
  future: BaseCommand[];
}

const commandMap = new Map<
  string,
  {
    unexecute: (command: BaseCommand, canvas: Canvas, slides: Slide[]) => void;
    execute: (command: BaseCommand, canvas: Canvas, slides: Slide[]) => void;
  }
>();

export const registerCommand = (
  name: string,
  resolve: {
    unexecute: (command: BaseCommand, canvas: Canvas, slides: Slide[]) => void;
    execute: (command: BaseCommand, canvas: Canvas, slides: Slide[]) => void;
  },
): void => {
  commandMap.set(name, resolve);
};

export const execute = (
  command: BaseCommand,
  canvas: Canvas,
  slides: Slide[],
  history: History,
): void => {
  const resolver = commandMap.get(command.type);

  if (resolver) {
    resolver.execute(command, canvas, slides);

    history.past.push(command);
    history.future = [];
  }
};

export const undo = (
  canvas: Canvas,
  slides: Slide[],
  history: History,
): void => {
  const items = [];
  let notFantomItemFound = false;

  for (let i = history.past.length - 1; i >= 0; --i) {
    items.unshift(history.past[i]);

    if (!history.past[i].isFantom) {
      notFantomItemFound = true;

      if (items.length !== 1) {
        items.shift();
      }
    }

    if (notFantomItemFound) {
      break;
    }
  }

  if (notFantomItemFound) {
    for (let i = 0; i < items.length; i++) {
      const command = history.past.pop();

      if (command) {
        const resolver = commandMap.get(command.type);
        resolver.unexecute(command, canvas, slides);
        history.future.push(command);
      }
    }
  } else {
    history.past = [];
  }
};

export const redo = (
  canvas: Canvas,
  slides: Slide[],
  history: History,
): void => {
  const items = [];
  let notFantomItemFound = false;

  for (let i = history.future.length - 1; i >= 0; --i) {
    items.unshift(history.past[i]);

    if (!history.future[i].isFantom) {
      notFantomItemFound = true;

      if (items.length !== 1) {
        items.shift();
      }
    }

    if (notFantomItemFound) {
      break;
    }
  }

  if (notFantomItemFound) {
    for (let i = 0; i < items.length; i++) {
      const command = history.future.pop();

      if (command) {
        const resolver = commandMap.get(command.type);
        resolver.execute(command, canvas, slides);
        history.past.push(command);
      }
    }
  } else {
    history.future = [];
  }
};
