import * as React from "react";

import {
  ControlPanelProps,
  EditorUIFactory,
  LayerControlPanelProps,
  ShapeControlProps,
  SlidesControlPanelProps,
} from "../../application/ui/EditorUIFactory";
import MuiControlPanel from "./MuiControlPanel";
import MuiShapeControlPanel from "./MuiShapeControlPanel";
import MuiLayersControlPanel from "./MuiLayersControlPanel";
import MuiSlidesControlPanel from "./MuiSlidesControlPanel";

export class MuiEditorFactoryImpl extends EditorUIFactory {
  ControlPanel(props: ControlPanelProps): JSX.Element {
    return <MuiControlPanel {...props} />;
  }

  ShapeControl(props: ShapeControlProps): JSX.Element {
    return <MuiShapeControlPanel {...props} />;
  }

  LayersPanel(props: LayerControlPanelProps): JSX.Element {
    return <MuiLayersControlPanel {...props} />;
  }

  SlidesPanel(props: SlidesControlPanelProps): JSX.Element {
    return <MuiSlidesControlPanel {...props} />;
  }
}
