import * as React from "react";
import { connect } from "react-redux";
import "./styles/canvas.scss";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { Drawable } from "../model/Drawable";
import DrawableContainer from "./DrawableContainer";
import DrawableFactory from "./DrawableFactory";

export class CanvasProps {
  items: Drawable[];
  cw: number;
  ch: number;
  sw: number;
  sh: number;
}

export class Canvas extends React.Component<
  CanvasProps & DispatchProps,
  unknown
> {
  private containerRef: HTMLElement | null;

  constructor(props: CanvasProps & DispatchProps) {
    super(props);
  }

  componentDidMount(): void {
    window.addEventListener("resize", this.resizeCanvas.bind(this));
    this.resizeCanvas();
  }

  componentWillUnmount(): void {
    window.removeEventListener("resize", this.resizeCanvas.bind(this));
  }

  resizeCanvas(): void {
    const { sw, sh } = this.props;

    const { offsetWidth, offsetHeight } = this.containerRef;

    const aspectRatio = sh / sw;

    this.props.onResizeWindow({
      width:
        offsetHeight > offsetWidth * aspectRatio
          ? offsetWidth
          : offsetHeight / aspectRatio,
      height:
        offsetHeight > offsetWidth * aspectRatio
          ? offsetWidth * aspectRatio
          : offsetHeight,
    });
  }

  render(): JSX.Element {
    const { cw, ch, items } = this.props;

    return (
      <div className="canvas-container" ref={(e) => (this.containerRef = e)}>
        <div
          className="canvas-workspace"
          style={{
            width: cw,
            height: ch,
          }}
        >
          {items.map((i) => (
            <DrawableContainer
              id={i.id}
              key={i.id}
              render={DrawableFactory}
              item={i}
            />
          ))}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    items: editor.canvas.items,
    sw: editor.canvas.standardWidth,
    sh: editor.canvas.standardHeight,
    cw: editor.canvas.width,
    ch: editor.canvas.height,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(Canvas);
