import * as React from "react";
import { connect } from "react-redux";
import {
  createMapStateToProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { Slide } from "../model/Canvas";
import "./styles/slide-preview.scss";
import DrawableFactory from "./DrawableFactory";
import { Drawable } from "../model/Drawable";

interface SlidePreviewProps {
  slide: Slide;
  sw: number;
  sh: number;
}

interface SlidePreviewState {
  width: number;
  height: number;
}

class SlidePreview extends React.Component<
  SlidePreviewProps,
  SlidePreviewState
> {
  private containerRef: HTMLElement | null;

  private readonly onResizeCanvas: () => void;

  constructor(props: SlidePreviewProps) {
    super(props);

    this.state = {
      width: 0,
      height: 0,
    };

    this.onResizeCanvas = this.resizeCanvas.bind(this);
  }

  componentDidMount(): void {
    window.addEventListener("resize", this.onResizeCanvas);
    this.resizeCanvas();
  }

  componentWillUnmount(): void {
    window.removeEventListener("resize", this.onResizeCanvas);
  }

  resizeCanvas(): void {
    const { sw, sh } = this.props;

    const { offsetWidth, offsetHeight } = this.containerRef;

    const aspectRatio = sh / sw;

    this.setState({
      width:
        offsetHeight > offsetWidth * aspectRatio
          ? offsetWidth
          : offsetHeight / aspectRatio,
      height:
        offsetHeight > offsetWidth * aspectRatio
          ? offsetWidth * aspectRatio
          : offsetHeight,
    });
  }

  render(): JSX.Element {
    const { slide, sw } = this.props;
    const { width, height } = this.state;

    return (
      <div
        className="slide-preview-container"
        ref={(e) => (this.containerRef = e)}
      >
        <div className="slide-preview-slide" style={{ width, height }}>
          {slide.items.map((s) => {
            const { x, y, w, h } = s.dimensions;

            const drawable: Drawable = {
              ...s,
              dimensions: {
                ...s.dimensions,
                x: (x * width) / sw,
                y: (y * width) / sw,
                w: (w * width) / sw,
                h: (h * width) / sw,
              },
              text: {
                ...s.text,
                size: (s.text.size * width) / sw,
              },
            };

            return (
              <DrawableFactory
                key={s.id}
                {...drawable}
                onClick={() => console.log()}
                onChangeText={() => console.log()}
                isActive={false}
              />
            );
          })}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    sw: editor.canvas.standardWidth,
    sh: editor.canvas.standardHeight,
  }));

export default connect(mapStateToProps, mapDispatchToProps)(SlidePreview);
