import { BaseCommand, registerCommand } from "./History";
import { Drawable } from "./Drawable";
import { Canvas } from "./Canvas";

const INCLUDE_ITEM_COMMAND = "INCLUDE_ITEM_COMMAND";

interface IncludeItemCommand extends BaseCommand {
  type: typeof INCLUDE_ITEM_COMMAND;
  item: Drawable;
}

export const makeIncludeItemCommand = (e: Drawable): IncludeItemCommand => {
  return {
    isFantom: false,
    type: INCLUDE_ITEM_COMMAND,
    item: e,
  };
};

registerCommand(INCLUDE_ITEM_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as IncludeItemCommand;
    ctx.items = ctx.items.filter((i) => i.id !== command.item.id);
    ctx.selectedItemId = null;
  },
  execute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as IncludeItemCommand;
    ctx.items.push(command.item);
  },
});
