import * as React from "react";
import { UIFactory as CommonUIFactory } from "../../../common/infrastructure/UIFactory";
import Canvas from "./Canvas";
import ControlPanel from "./ControlPanel";
import ItemControlPanel from "./ItemControlPanel";
import LayersPanel from "./LayersPanel";
import "./styles/editor.scss";
import SlidesPanel from "./SlidesPanel";
import { DispatchProps, mapDispatchToProps } from "../../infrastructure/redux";
import { connect } from "react-redux";
import { Grid, Hidden } from "@material-ui/core";

class Editor extends React.Component<DispatchProps, unknown> {
  private readonly onKeyDown: (e: KeyboardEvent) => void;

  constructor(props: DispatchProps) {
    super(props);

    const fn = (e) => {
      const { onRedo, onUndo, onInsertFromBuffer } = this.props;

      if (e.ctrlKey) {
        if (e.code === "KeyV") {
          onInsertFromBuffer();
        }
        if (e.shiftKey && e.code === "KeyZ") {
          onRedo();
        } else {
          if (e.code === "KeyZ") {
            onUndo();
          }
        }
      }
    };

    this.onKeyDown = fn.bind(this);
  }

  componentDidMount() {
    document.addEventListener("keydown", this.onKeyDown);
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.onKeyDown);
  }

  render(): JSX.Element {
    return (
      <div className="editor-container">
        <div>
          <CommonUIFactory.Container elevation={0} withIndent>
            <ControlPanel />
          </CommonUIFactory.Container>
        </div>
        <div className="editor-workspace">
          <Hidden mdUp>
            <div
              style={{ height: "300px", flexShrink: 1, margin: "20px 0" }}
              className="editor-canvas-container"
            >
              <Canvas />
            </div>
          </Hidden>
          <CommonUIFactory.Grid
            container
            alignItems="stretch"
            style={{ flexGrow: 1 }}
          >
            <CommonUIFactory.Grid item xs={5} sm={5} md={2} lg={2} xl={1}>
              <CommonUIFactory.Container
                elevation={0}
                className="editor-side-container"
              >
                <SlidesPanel />
              </CommonUIFactory.Container>
            </CommonUIFactory.Grid>

            <CommonUIFactory.Grid
              mdDown
              item
              xs={7}
              sm={7}
              md={7}
              lg={8}
              xl={9}
              style={{ padding: 20 }}
            >
              <div className="editor-canvas-container">
                <Canvas />
              </div>
            </CommonUIFactory.Grid>

            <CommonUIFactory.Grid item xs={7} sm={7} md={3} lg={2} xl={2}>
              <CommonUIFactory.Container
                elevation={0}
                className="editor-side-container"
              >
                <ItemControlPanel />
                <LayersPanel />
              </CommonUIFactory.Container>
            </CommonUIFactory.Grid>
          </CommonUIFactory.Grid>
        </div>
      </div>
    );
  }
}

export default connect(null, mapDispatchToProps)(Editor);
