import { BaseCommand, registerCommand } from "./History";
import { Drawable } from "./Drawable";
import { Canvas } from "./Canvas";

const UPDATE_ITEM_COMMAND = "UPDATE_ITEM_COMMAND";

interface UpdateItemCommand extends BaseCommand {
  type: typeof UPDATE_ITEM_COMMAND;
  beforeUpdateItem: Drawable;
  afterUpdateItem: Drawable;
}

export const makeUpdateItemCommand = (e: {
  before: Drawable;
  after: Drawable;
}): UpdateItemCommand => {
  return {
    isFantom: false,
    type: UPDATE_ITEM_COMMAND,
    beforeUpdateItem: e.before,
    afterUpdateItem: e.after,
  };
};

registerCommand(UPDATE_ITEM_COMMAND, {
  unexecute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as UpdateItemCommand;

    ctx.items = ctx.items.map((s) => {
      if (s.id !== command.beforeUpdateItem.id) {
        return s;
      }

      return command.beforeUpdateItem;
    });
  },
  execute: (cmd: BaseCommand, ctx: Canvas): void => {
    const command = cmd as UpdateItemCommand;
    ctx.items = ctx.items.map((s) => {
      if (s.id !== command.beforeUpdateItem.id) {
        return s;
      }

      return command.afterUpdateItem;
    });
  },
});
