import {
  ControlPanelProps,
  EditorUIFactory,
  LayerControlPanelProps,
  ShapeControlProps,
  SlidesControlPanelProps,
} from "../application/ui/EditorUIFactory";
import { MuiEditorFactoryImpl } from "./mui/MuiEditorFactoryImpl";

const factory: EditorUIFactory = new MuiEditorFactoryImpl();

export class UIFactory {
  public static ControlPanel(props: ControlPanelProps): JSX.Element {
    return factory.ControlPanel(props);
  }
  public static ShapeControl(props: ShapeControlProps): JSX.Element {
    return factory.ShapeControl(props);
  }

  public static LayersControlPanel(props: LayerControlPanelProps): JSX.Element {
    return factory.LayersPanel(props);
  }

  public static SlidesPanel(props: SlidesControlPanelProps): JSX.Element {
    return factory.SlidesPanel(props);
  }
}
