import * as React from "react";
import { Drawable, ItemTypes } from "../model/Drawable";
import { UIFactory as EditorUIFactory } from "../../infrastructure/UIFactory";
import {
  createMapStateToProps,
  DispatchProps,
  mapDispatchToProps,
} from "../../infrastructure/redux";
import { connect } from "react-redux";

interface ItemControlPanelProps extends DispatchProps {
  selectedItemId: string;
  item: Drawable | null;
}

interface ItemControlPanelState {
  color: string;
}

class ItemControlPanel extends React.Component<
  ItemControlPanelProps,
  ItemControlPanelState
> {
  constructor(props: ItemControlPanelProps) {
    super(props);

    this.state = {
      color: props.item ? props.item.appearance.color : "",
    };
  }

  onChangeDimensions(e: {
    x: number;
    y: number;
    w: number;
    h: number;
    angle: number;
  }): void {
    const { onChangeDimensionsManually } = this.props;
    onChangeDimensionsManually({ id: this.props.selectedItemId, ...e });
  }

  onChangeColor(e: { color: string }): void {
    this.setState({ color: e.color });
  }

  onChangeColorOver(e: { color: string }): void {
    this.setState({ color: e.color });

    const { onUpdateItem } = this.props;

    onUpdateItem({
      id: this.props.selectedItemId,
      appearance: { color: e.color },
    });
  }

  onChangeFont(e: {
    font: string;
    size: number;
    bold: boolean;
    italic: boolean;
    underline: boolean;
    alignment: "left" | "center" | "right";
  }): void {
    const { onUpdateItem } = this.props;

    onUpdateItem({ id: this.props.selectedItemId, text: { ...e } });
  }

  componentDidUpdate(prevProps: Readonly<ItemControlPanelProps>) {
    if (this.props.selectedItemId != prevProps.selectedItemId) {
      if (this.props.item) {
        this.setState({ color: this.props.item.appearance.color });
      }
    }
  }

  render(): JSX.Element {
    const { item, onDeleteItem, selectedItemId } = this.props;

    const { color } = this.state;

    return (
      <React.Fragment>
        {this.props.selectedItemId && (
          <EditorUIFactory.ShapeControl
            x={item ? Math.round(item.dimensions.x) : 0}
            y={item ? Math.round(item.dimensions.y) : 0}
            w={item ? Math.round(item.dimensions.w) : 0}
            h={item ? Math.round(item.dimensions.h) : 0}
            angle={item ? item.dimensions.angle : 0}
            color={color}
            onChangeDimensions={(e) => this.onChangeDimensions(e)}
            onChangeColor={(e) => this.onChangeColor(e)}
            onChangeColorOver={(e) => this.onChangeColorOver(e)}
            font={item ? item.text.font : ""}
            size={item ? item.text.size : 14}
            bold={item ? item.text.bold : false}
            italic={item ? item.text.italic : false}
            underline={item ? item.text.underline : false}
            onChangeFont={(e) => this.onChangeFont(e)}
            onCopyItem={() => true}
            onDeleteItem={() => onDeleteItem({ id: selectedItemId })}
            alignment={item ? item.text.alignment : "left"}
            displayFontPanel={item?.type === ItemTypes.TEXT}
          />
        )}
      </React.Fragment>
    );
  }
}

const mapStateToProps = (s, o) =>
  createMapStateToProps(s, o, (editor) => ({
    selectedItemId: editor.canvas.selectedItemId,
    item: editor.canvas.items.find(
      (s) => s.id === editor.canvas.selectedItemId,
    ),
  }));

export default connect(mapStateToProps, mapDispatchToProps)(ItemControlPanel);
