import * as React from "react";
import { CSSProperties } from "react";

import "./styles/index.scss";
import {
  calcNewLocation,
  degToRadian,
  countAngleBetweenVectors,
  SurfacePointers,
} from "./utils/math";

export interface Props {
  x: number;
  y: number;
  w: number;
  h: number;
  angle: number;
  onMoveStart?: () => void;
  onMove: (position: { dx: number; dy: number }) => void;
  onMoveOver?: () => void;
  onResizeStart?: () => void;
  onResize: (position: { x: number; y: number; w: number; h: number }) => void;
  onResizeOver?: () => void;
  onRotateStart?: () => void;
  onRotate: (position: { angle: number }) => void;
  onRotateOver?: () => void;
  onKeyDown?: (e: KeyboardEvent) => void;
  minWidth?: number;
  minHeight?: number;
  aspectRatio?: number;
  className?: string;
  style?: CSSProperties;
}

export default class DragDrop extends React.Component<Props, unknown> {
  mainBoxRef: HTMLDivElement | null;

  bottomBorder: HTMLDivElement | null;

  constructor(props: Props) {
    super(props);
  }

  componentDidMount(): void {
    this.bottomBorder.addEventListener("dragstart", (e) => e.preventDefault());
  }

  onMove(mouseDownEvent: MouseEvent): void {
    let { clientX: startX, clientY: startY } = mouseDownEvent;

    const onpointermove = (pointermoveEvent: MouseEvent) => {
      pointermoveEvent.preventDefault();
      pointermoveEvent.stopImmediatePropagation();

      const { onMove } = this.props;

      const { clientX, clientY } = pointermoveEvent,
        dx = clientX - startX,
        dy = clientY - startY;

      onMove({ dx, dy });

      startX = clientX;
      startY = clientY;
    };

    window.addEventListener("pointermove", onpointermove);

    window.addEventListener(
      "pointerup",
      () => {
        const { onMoveOver } = this.props;
        window.removeEventListener("pointermove", onpointermove);
        onMoveOver ? onMoveOver() : null;
      },
      { once: true },
    );
  }

  onRotate(mouseDownEvent: MouseEvent): void {
    mouseDownEvent.preventDefault();

    if (mouseDownEvent.button !== 0) {
      return;
    }

    const { clientX, clientY } = mouseDownEvent;
    const { onRotate } = this.props;
    const startAngle = this.props.angle;

    const rect = this.mainBoxRef.getBoundingClientRect();

    const center = {
      x: rect.left + rect.width / 2,
      y: rect.top + rect.height / 2,
    };

    const startVector = {
      x: clientX - center.x,
      y: clientY - center.y,
    };

    const onMove = (e) => {
      e.preventDefault();
      e.stopImmediatePropagation();
      const { clientX, clientY } = e;
      const rotateVector = {
        x: clientX - center.x,
        y: clientY - center.y,
      };

      const angle = countAngleBetweenVectors(startVector, rotateVector);

      let rotateAngle = Math.round(startAngle + angle);
      if (rotateAngle >= 360) {
        rotateAngle -= 360;
      } else if (rotateAngle < 0) {
        rotateAngle += 360;
      }
      if (rotateAngle > 356 || rotateAngle < 4) {
        rotateAngle = 0;
      } else if (rotateAngle > 86 && rotateAngle < 94) {
        rotateAngle = 90;
      } else if (rotateAngle > 176 && rotateAngle < 184) {
        rotateAngle = 180;
      } else if (rotateAngle > 266 && rotateAngle < 274) {
        rotateAngle = 270;
      }

      onRotate({ angle: rotateAngle });
    };

    document.addEventListener("pointermove", onMove);

    document.addEventListener(
      "pointerup",
      () => {
        const { onRotateOver } = this.props;
        document.removeEventListener("pointermove", onMove);
        onRotateOver ? onRotateOver() : null;
      },
      { once: true },
    );
  }

  onResize(e: MouseEvent, type: SurfacePointers): void {
    e.preventDefault();

    const { x, y, w: width, h: height, angle: rotateAngle } = this.props;

    const centerX = x + width / 2,
      centerY = y + height / 2;

    const { clientX: startX, clientY: startY } = e;

    const onMove = (e) => {
      e.stopImmediatePropagation();
      const { clientX, clientY } = e,
        deltaX = clientX - startX,
        deltaY = clientY - startY;

      this.makeResize(
        Math.sqrt(deltaX * deltaX + deltaY * deltaY),
        Math.atan2(deltaY, deltaX),
        { width, height, centerX, centerY, rotateAngle },
        type,
      );
    };

    document.addEventListener("pointermove", onMove);
    document.addEventListener(
      "pointerup",
      () => {
        const { onResizeOver } = this.props;
        document.removeEventListener("pointermove", onMove);
        onResizeOver ? onResizeOver() : null;
      },
      { once: true },
    );
  }

  makeResize(
    length: number,
    alpha: number,
    rect: {
      width: number;
      height: number;
      centerX: number;
      centerY: number;
      rotateAngle: number;
    },
    type: SurfacePointers,
  ): void {
    const {
      angle: rotateAngle,
      onResize,
      aspectRatio,
      minWidth,
      minHeight,
    } = this.props;

    const beta = alpha - degToRadian(rotateAngle),
      deltaW = length * Math.cos(beta),
      deltaH = length * Math.sin(beta);

    const { width, height, top, left } = calcNewLocation(
      type,
      { ...rect, rotateAngle },
      deltaW,
      deltaH,
      aspectRatio ?? false,
      minWidth ?? 25,
      minHeight ?? 25,
    );

    onResize({ x: left, y: top, h: height, w: width });
  }

  render(): JSX.Element {
    const {
      x,
      y,
      w,
      h,
      angle,
      className,
      style,
      onMoveStart,
      onResizeStart,
      onRotateStart,
      onKeyDown,
    } = this.props;

    return (
      <React.Fragment>
        <div
          className={`area-selector-main ${className}`}
          style={{
            ...style,
            width: w,
            height: h,
            top: y,
            left: x,
            transform: `rotate(${angle}deg)`,
          }}
          ref={(el) => {
            this.mainBoxRef = el;
          }}
          onDragStart={(e) => e.preventDefault()}
        >
          <div
            className="area-selector-rotate"
            onMouseDown={(e) => {
              onResizeStart ? onRotateStart() : null;
              this.onRotate(e.nativeEvent);
            }}
          >
            <i />
          </div>
          <div className="area-selector-top-border" />
          <div className="area-selector-left-border" />
          <div className="area-selector-right-border" />
          <div
            className="area-selector-bottom-border"
            ref={(el) => (this.bottomBorder = el)}
            onPointerDown={(e) => {
              onMoveStart ? onMoveStart() : null;
              this.onMove(e.nativeEvent);
            }}
            tabIndex={0}
            onKeyDown={(e) => (onKeyDown ? onKeyDown(e.nativeEvent) : null)}
          />
          <div
            className="area-selector-dot area-selector-dot-top-left"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.TOP_LEFT);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-top-middle"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.TOP);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-top-right"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.TOP_RIGHT);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-left"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.LEFT);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-right"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.RIGHT);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-bottom-left"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.BOTTOM_LEFT);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-bottom-middle"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.BOTTOM);
            }}
          />
          <div
            className="area-selector-dot area-selector-dot-bottom-right"
            onPointerDown={(e) => {
              onResizeStart ? onResizeStart() : null;
              this.onResize(e.nativeEvent, SurfacePointers.BOTTOM_RIGHT);
            }}
          />
        </div>
      </React.Fragment>
    );
  }
}
